import React, { useEffect, useState } from 'react';
import './Dashboard.css';
import { useHistory } from 'react-router-dom';
import { useApp } from './AppContext';
import {
  Button,
  Card,
  CardContent,
  CardActions,
  Chip,
  CircularProgress,
  Tabs,
  Tab,
  Typography,
} from '@mui/material';

function Dashboard() {
  const history = useHistory();
  const {
    currentUser,
    isAuthenticated,
    loading,
    getUserBookings,
    fetchBookings,
    updateBookingStatus,

    // admin bits from AppContext
    isAdmin,
    landlordApplications,
    fetchLandlordApplications,
    approveLandlordApplication,
    rejectLandlordApplication,
  } = useApp();

  const [tab, setTab] = useState(0);
  const [loadingBookings, setLoadingBookings] = useState(false);
  const [loadingApplications, setLoadingApplications] = useState(false);

  // Redirect to login if not authenticated
  useEffect(() => {
    if (!loading && !isAuthenticated) {
      history.replace('/login');
    }
  }, [loading, isAuthenticated, history]);

  // Load bookings (and landlord applications for admins)
  useEffect(() => {
    const load = async () => {
      if (!isAuthenticated) return;

      setLoadingBookings(true);
      await fetchBookings();
      setLoadingBookings(false);

      if (isAdmin) {
        setLoadingApplications(true);
        await fetchLandlordApplications();
        setLoadingApplications(false);
      }
    };
    load();
  }, [isAuthenticated, fetchBookings, isAdmin, fetchLandlordApplications]);

  if (loading || (!isAuthenticated && !loading)) {
    return (
      <div className="dashboard dashboard--center">
        <CircularProgress />
      </div>
    );
  }

  if (!currentUser) {
    return (
      <div className="dashboard dashboard--center">
        <Typography variant="body1">Please log in to view your dashboard.</Typography>
      </div>
    );
  }

  const allBookings = getUserBookings();

  const bookingsByStatus = {
    all: allBookings,
    pending: allBookings.filter((b) => b.status === 'pending'),
    approved: allBookings.filter((b) => b.status === 'approved'),
    rejected: allBookings.filter((b) => b.status === 'rejected'),
    cancelled: allBookings.filter((b) => b.status === 'cancelled'),
  };

  const tabKeys = ['all', 'pending', 'approved', 'rejected', 'cancelled'];
  const currentKey = tabKeys[tab] || 'all';
  const bookingsToShow = bookingsByStatus[currentKey];

  const isBoarder = currentUser.user_type === 'student'; // DB still uses 'student'
  const isLandlord = currentUser.user_type === 'landlord';

  const handleStatusChange = async (bookingId, status) => {
    try {
      await updateBookingStatus(bookingId, status);
    } catch (error) {
      console.error('updateBookingStatus error:', error);
      alert(error?.message || 'Failed to update booking. Please try again.');
    }
  };

  const statusChipColor = (status) => {
    switch (status) {
      case 'approved':
        return 'success';
      case 'pending':
        return 'warning';
      case 'rejected':
      case 'cancelled':
        return 'default';
      default:
        return 'default';
    }
  };

  const statusLabel = (status) => {
    switch (status) {
      case 'approved':
        return 'Approved';
      case 'pending':
        return 'Pending';
      case 'rejected':
        return 'Rejected';
      case 'cancelled':
        return 'Cancelled';
      default:
        return status;
    }
  };

  return (
    <div className="dashboard">
      {/* Header stays the same */}
      <div className="dashboard__header">
        <div>
          <Typography variant="h5">
            {isBoarder ? 'My Bookings' : 'Booking Requests'}
          </Typography>
          <Typography variant="body2" color="textSecondary">
            Chat to confirm visitation dates, move-in dates, and any other details.
          </Typography>
        </div>
      </div>

      {/* If not landlord yet: CTA to apply */}
      {!isLandlord && (
        <div className="dashboard__ctaRow">
          <Typography variant="body2" color="textSecondary">
            Want to list your own property?
          </Typography>
          <Button
            variant="outlined"
            size="small"
            onClick={() => history.push('/apply-landlord')}
            style={{ marginLeft: 8 }}
          >
            Apply as landlord
          </Button>
        </div>
      )}

      {/* Admin section: landlord applications + approve/reject */}
      {isAdmin && (
        <div className="dashboard__adminSection">
          <div className="dashboard__adminHeader">
            <Typography variant="h6">Landlord applications</Typography>
            <Typography variant="body2" color="textSecondary">
              Review business permits and approve or reject landlord accounts.
            </Typography>
          </div>

          {loadingApplications ? (
            <div className="dashboard__loading">
              <CircularProgress size={20} />
            </div>
          ) : landlordApplications.length === 0 ? (
            <div className="dashboard__empty">
              <Typography variant="body2" color="textSecondary">
                No pending applications right now.
              </Typography>
            </div>
          ) : (
            <div className="dashboard__list dashboard__list--applications">
              {landlordApplications.map((app) => (
                <Card key={app.id} className="dashboard__card">
                  <CardContent>
                    <div className="dashboard__cardHeader">
                      <Typography
                        variant="subtitle1"
                        className="dashboard__cardTitle"
                      >
                        {app.full_name || 'Unnamed applicant'}
                      </Typography>
                      <Chip
                        size="small"
                        label={app.status || 'PENDING'}
                        color={app.status === 'APPROVED' ? 'success' : app.status === 'REJECTED' ? 'default' : 'warning'}
                      />
                    </div>

                    <Typography variant="body2" color="textSecondary">
                      Email: {app.email}
                    </Typography>

                    {app.business_name && (
                      <Typography variant="body2" color="textSecondary">
                        Business name: {app.business_name}
                      </Typography>
                    )}

                    <Typography variant="body2" color="textSecondary">
                      Submitted:{' '}
                      {app.created_at
                        ? new Date(app.created_at).toLocaleString()
                        : ''}
                    </Typography>

                    {app.permit_path && (
                      <Typography
                        variant="body2"
                        color="textSecondary"
                        style={{ marginTop: 4 }}
                      >
                        Permit file path: {app.permit_path}
                        {/* You can open this in Supabase Storage UI using this path */}
                      </Typography>
                    )}

                    {app.rejection_reason && (
                      <Typography
                        variant="body2"
                        color="textSecondary"
                        style={{ marginTop: 4 }}
                      >
                        Last rejection reason: {app.rejection_reason}
                      </Typography>
                    )}
                  </CardContent>

                  <CardActions className="dashboard__cardActions">
                    <Button
                      size="small"
                      color="primary"
                      variant="contained"
                      onClick={async () => {
                        const ok = window.confirm(
                          'Approve this landlord application? This will switch the user to landlord.'
                        );
                        if (!ok) return;
                        try {
                          await approveLandlordApplication(app.id);
                        } catch (err) {
                          console.error('approveLandlordApplication error:', err);
                          alert(
                            err?.message ||
                              'Failed to approve application. Please try again.'
                          );
                        }
                      }}
                    >
                      Approve
                    </Button>
                    <Button
                      size="small"
                      color="secondary"
                      variant="outlined"
                      style={{ marginLeft: 8 }}
                      onClick={async () => {
                        const reason = window.prompt(
                          'Optional: enter a rejection reason (this will be stored with the application):',
                          ''
                        );
                        const ok = window.confirm('Reject this application?');
                        if (!ok) return;
                        try {
                          await rejectLandlordApplication(app.id, reason || null);
                        } catch (err) {
                          console.error('rejectLandlordApplication error:', err);
                          alert(
                            err?.message ||
                              'Failed to reject application. Please try again.'
                          );
                        }
                      }}
                    >
                      Reject
                    </Button>
                  </CardActions>
                </Card>
              ))}
            </div>
          )}
        </div>
      )}

      {/* Tabs for bookings (unchanged) */}
      <div className="dashboard__tabs">
        <Tabs
          value={tab}
          onChange={(_, newValue) => setTab(newValue)}
          indicatorColor="primary"
          textColor="primary"
          variant="scrollable"
          scrollButtons="auto"
        >
          <Tab label={`All (${bookingsByStatus.all.length})`} />
          <Tab label={`Pending (${bookingsByStatus.pending.length})`} />
          <Tab label={`Approved (${bookingsByStatus.approved.length})`} />
          <Tab label={`Rejected (${bookingsByStatus.rejected.length})`} />
          <Tab label={`Cancelled (${bookingsByStatus.cancelled.length})`} />
        </Tabs>
      </div>

      {loadingBookings ? (
        <div className="dashboard__loading">
          <CircularProgress />
        </div>
      ) : bookingsToShow.length === 0 ? (
        <div className="dashboard__empty">
          <Typography variant="body2" color="textSecondary">
            No bookings in this category yet.
          </Typography>
        </div>
      ) : (
        <div className="dashboard__list">
          {bookingsToShow.map((b) => {
            const otherName = isBoarder ? b.landlord_name : b.student_name;
            const otherRole = isBoarder ? 'Landlord' : 'Boarder';

            return (
              <Card key={b.id} className="dashboard__card">
                <CardContent>
                  <div className="dashboard__cardHeader">
                    <Typography variant="subtitle1" className="dashboard__cardTitle">
                      {b.listing_title}
                    </Typography>
                    <Chip
                      size="small"
                      label={statusLabel(b.status)}
                      color={statusChipColor(b.status)}
                    />
                  </div>

                  <Typography variant="body2" color="textSecondary">
                    {otherRole}: {otherName}
                  </Typography>

                  <Typography variant="body2" color="textSecondary">
                    Move-in date: {b.move_in_date || 'Not set'}
                  </Typography>

                  <Typography variant="body2" color="textSecondary">
                    Visitation date: {b.visitation_date || 'Not set'}
                  </Typography>

                  {b.notes && (
                    <Typography
                      variant="body2"
                      color="textSecondary"
                      style={{ marginTop: 4 }}
                    >
                      Your message: {b.notes}
                    </Typography>
                  )}
                </CardContent>

                <CardActions className="dashboard__cardActions">
                  <Button
                    size="small"
                    variant="outlined"
                    onClick={() => history.push(`/bookings/${b.id}/chat`)}
                  >
                    Open chat
                  </Button>

                  {isLandlord && b.status === 'pending' && (
                    <>
                      <Button
                        size="small"
                        color="primary"
                        onClick={() => handleStatusChange(b.id, 'approved')}
                      >
                        Approve
                      </Button>
                      <Button
                        size="small"
                        color="secondary"
                        onClick={() => handleStatusChange(b.id, 'rejected')}
                      >
                        Reject
                      </Button>
                    </>
                  )}
                </CardActions>
              </Card>
            );
          })}
        </div>
      )}
    </div>
  );
}

export default Dashboard;
