import React, { useState } from 'react';
import './Home.css';
import { useHistory } from 'react-router-dom';
import { useApp } from './AppContext';
import { Button, TextField } from '@mui/material';
import Map from './Map';

function Home() {
  const history = useHistory();
  const { listings, setFilters, getFilteredListings } = useApp();

  const [searchText, setSearchText] = useState('');

  const handleSearchSubmit = (e) => {
    e.preventDefault();
    setFilters((prev) => ({
      ...prev,
      searchQuery: searchText,
    }));
    history.push('/search');
  };

  const featured = (getFilteredListings?.() || listings || []).slice(0, 3);

  return (
    <div className="home">
      {/* Hero with search */}
      <section className="home__hero">
        <div className="home__heroInner">
          <div className="home__heroTextBlock">
            <h1>Find a place that feels like home near your campus</h1>
            <p>
              DormFinder lets boarders and landlords connect, schedule visitations, and
              confirm move-in dates  all in one place.
            </p>
          </div>

          <form className="home__searchForm" onSubmit={handleSearchSubmit}>
            <TextField
              placeholder="Search by school, city, or accommodation name"
              value={searchText}
              onChange={(e) => setSearchText(e.target.value)}
              fullWidth
              variant="outlined"
              size="medium"
            />
            <Button
              variant="contained"
              color="primary"
              type="submit"
              className="home__searchButton"
            >
              Search accommodation
            </Button>
          </form>
        </div>
      </section>

      {/* Find your accommodation section */}
      <section className="home__section">
        <div className="home__sectionHeader">
          <h2>Find your accommodation</h2>
          <p>
            Explore featured listings. Use filters on the search page to narrow down by
            price, room type, gender preference, and amenities.
          </p>
        </div>

        {featured.length === 0 ? (
          <p className="home__empty">
            No listings yet. Landlords can add their properties from the dashboard.
          </p>
        ) : (
          <div className="home__cards">
            {featured.map((listing) => (
              <div
                key={listing.id}
                className="home__card"
                onClick={() => history.push(`/listing/${listing.id}`)}
              >
                <div className="home__cardImageWrapper">
                  <img
                    src={
                      listing.img ||
                      'https://images.unsplash.com/photo-1560185127-6ed189bf02f4?w=600'
                    }
                    alt={listing.title}
                    className="home__cardImage"
                  />
                </div>
                <div className="home__cardBody">
                  <h3>{listing.title}</h3>
                  <p className="home__cardLocation">{listing.location}</p>
                  <p className="home__cardPrice">
                    {Number(listing.price || 0).toLocaleString()} / month
                  </p>
                  {listing.distance && (
                    <p className="home__cardMeta">{listing.distance}</p>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}

        <div className="home__sectionFooter">
          <Button
            variant="outlined"
            color="primary"
            onClick={() => history.push('/search')}
          >
            View all accommodations
          </Button>
        </div>
      </section>

      {/* Map section */}
      <section className="home__mapSection">
        <div className="home__mapHeader">
          <h2>Explore accommodations on the map</h2>
          <p>
            See which options are closest to your campus, transport hubs, and favorite
            places.
          </p>
        </div>
        <div className="home__mapWrapper">
          <Map />
        </div>
      </section>
    </div>
  );
}

export default Home;
