import React from 'react';
import './SupportDeveloper.css';
import { Button } from '@mui/material';
import { useHistory } from 'react-router-dom';

function SupportDeveloper() {
  const history = useHistory();

  return (
    <div className="supportDev">
      <div className="supportDev__card">
        <h1>Support the Developer </h1>
        <p className="supportDev__subtitle">
          If this app helped you find or host a great place, you can support the
          developer to keep improving DormFinder.
        </p>

        {/* Bank transfer section */}
        <div className="supportDev__section">
          <h2>Bank Transfer</h2>


          <div className="supportDev__imageWrapper">
            {/*  Replace /bank-support.png with your actual image file name */}
            <img
              src="/bpi.jpg"
              alt="Bank account details for supporting the developer"
              className="supportDev__image"
            />
          </div>


        </div>

        {/* GCash section */}
        <div className="supportDev__section">
          <h2>GCash</h2>


          <div className="supportDev__imageWrapper supportDev__imageWrapper--gcash">
            {/*  Replace /gcash-support.png with your actual GCash QR image */}
            <img
              src="/gcash.jpg"
              alt="GCash QR code for supporting the developer"
              className="supportDev__image"
            />
          </div>


        </div>

        <p className="supportDev__thanks">
          Thank you for supporting the developer! Every contribution helps keep this
          project alive and improving. 
        </p>

        <div className="supportDev__actions">
          <Button variant="outlined" onClick={() => history.push('/')}>
            Back to home
          </Button>
        </div>
      </div>
    </div>
  );
}

export default SupportDeveloper;
