import React, { useState, useEffect } from 'react';
import './SearchPage.css';
import { Button, Select, MenuItem, FormControl, Slider, Typography } from "@mui/material";
import SearchResult from "./SearchResult";
import { useApp } from './AppContext';

function SearchPage() {
    const { getFilteredListings, filters, setFilters, fetchListings } = useApp();
    const [showFilters, setShowFilters] = useState(false);
    const filteredListings = getFilteredListings();

    useEffect(() => {
        fetchListings();
    }, [fetchListings]);

    const handleRoomTypeChange = (e) => {
        setFilters({ ...filters, roomType: e.target.value });
    };

    const handleGenderChange = (e) => {
        setFilters({ ...filters, gender: e.target.value });
    };

    const handlePriceChange = (e, newValue) => {
        setFilters({ ...filters, priceRange: newValue });
    };

    return (
        <div className='searchPage'>
            <div className='searchPage__info'>
                <p className='searchPage__count'>
                    {filteredListings.length} {filteredListings.length === 1 ? 'room' : 'rooms'} available
                </p>
                <h1>Housings in Tuguegarao</h1>
                <div className='searchPage__filters'>
                    <FormControl variant="outlined" size="small" className="filter-control">
                        <Select
                            value={filters.roomType}
                            onChange={handleRoomTypeChange}
                            displayEmpty
                        >
                            <MenuItem value="all">All Room Types</MenuItem>
                            <MenuItem value="single">Single Room</MenuItem>
                            <MenuItem value="shared">Shared Room</MenuItem>
                            <MenuItem value="studio">Studio</MenuItem>
                            <MenuItem value="apartment">Apartment</MenuItem>
                        </Select>
                    </FormControl>

                    <FormControl variant="outlined" size="small" className="filter-control">
                        <Select
                            value={filters.gender}
                            onChange={handleGenderChange}
                            displayEmpty
                        >
                            <MenuItem value="all">Any Gender</MenuItem>
                            <MenuItem value="male">Male Only</MenuItem>
                            <MenuItem value="female">Female Only</MenuItem>
                        </Select>
                    </FormControl>

                    <Button 
                        variant="contained"
                        onClick={() => setShowFilters(!showFilters)}
                        className="price-filter-btn"
                    >
                        {showFilters ? 'Hide' : 'Show'} Price Filter
                    </Button>
                </div>

                {showFilters && (
                    <div className='searchPage__priceFilter'>
                        <Typography gutterBottom className="price-label">
                            Price Range: {filters.priceRange[0].toLocaleString()} - {filters.priceRange[1].toLocaleString()}
                        </Typography>
                        <Slider
                            value={filters.priceRange}
                            onChange={handlePriceChange}
                            valueLabelDisplay="auto"
                            min={0}
                            max={20000}
                            step={500}
                            className="price-slider"
                        />
                    </div>
                )}
            </div>

            {filteredListings.length === 0 ? (
                <div className='searchPage__noResults'>
                    <div className="noResults__icon"></div>
                    <h3>No listings found</h3>
                    <p>Try adjusting your filters or check back later for new listings</p>
                </div>
            ) : (
                <div className="searchPage__results">
                    {filteredListings.map((listing) => (
                        <SearchResult
                            key={listing.id}
                            id={listing.id}
                            img={listing.img}
                            location={`${listing.location} ${listing.distance ? '- ' + listing.distance : ''}`}
                            title={listing.title}
                            description={listing.description}
                            star={listing.star}
                            price={`${listing.price.toLocaleString()} / month`}
                            total={`${listing.price.toLocaleString()} monthly`}
                        />
                    ))}
                </div>
            )}
        </div>
    )
}

export default SearchPage
