import React, { useState } from 'react';
import './AddListing.css';
import { useHistory } from 'react-router-dom';
import { useApp } from './AppContext';
import { supabase } from './supabaseClient';
import LocationPickerMap from './LocationPickerMap';
import {
  TextField,
  Button,
  MenuItem,
  FormControl,
  InputLabel,
  Select,
  Chip,
  Input,
  CircularProgress,
} from '@mui/material';

const roomTypes = [
  { value: 'single', label: 'Single room' },
  { value: 'shared', label: 'Shared room' },
  { value: 'studio', label: 'Studio' },
  { value: 'apartment', label: 'Apartment' },
];

const genders = [
  { value: 'any', label: 'Any gender' },
  { value: 'male', label: 'Male only' },
  { value: 'female', label: 'Female only' },
];

const availableAmenities = [
  'WiFi',
  'Air Conditioning',
  'Study Desk',
  'Laundry',
  'Kitchen',
  'Private Bathroom',
  'Parking',
  'Security',
  'Meals Included',
  'Gym',
  'Study Area',
  'Pet-Friendly',
  'Utilities Included',
  'Open for visitation',
  'GCash (payment option)',
];

function AddListing() {
  const history = useHistory();
  const { addListing, isAuthenticated, currentUser } = useApp();

  const [formData, setFormData] = useState({
    title: '',
    description: '',
    location: '',
    distance: '',
    price: '',
    roomType: 'single',
    gender: 'any',
    amenities: [],
    img: '', // optional manual URL fallback
    latitude: '',
    longitude: '',
    landlordContact: '',
  });

  const [imageFiles, setImageFiles] = useState([]); // File[]
  const [submitting, setSubmitting] = useState(false);
  const [errorMsg, setErrorMsg] = useState('');

  if (!isAuthenticated || !currentUser || currentUser.user_type !== 'landlord') {
    return (
      <div className="addListing addListing--center">
        <div className="addListing__card">
          <h2>Add a listing</h2>
          {!isAuthenticated ? (
            <>
              <p>You need to log in as a landlord to add a listing.</p>
              <Button
                variant="contained"
                onClick={() => history.push('/login')}
              >
                Go to login
              </Button>
            </>
          ) : currentUser.user_type !== 'landlord' ? (
            <p>Only landlord accounts can add listings.</p>
          ) : null}
        </div>
      </div>
    );
  }

  const handleChange = (e) => {
    const { name, value } = e.target;

    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleAmenityChange = (e) => {
    const {
      target: { value },
    } = e;

    setFormData((prev) => ({
      ...prev,
      amenities: typeof value === 'string' ? value.split(',') : value,
    }));
  };

  const handleImageChange = (e) => {
    const files = Array.from(e.target.files || []);
    setImageFiles(files);
  };

  // Called when user drags the marker in LocationPickerMap
  const handleMapLocationChange = ({ latitude, longitude }) => {
    if (latitude == null || longitude == null) return;

    const latStr =
      typeof latitude === 'number' ? latitude.toFixed(6) : String(latitude);
    const lngStr =
      typeof longitude === 'number' ? longitude.toFixed(6) : String(longitude);

    setFormData((prev) => ({
      ...prev,
      latitude: latStr,
      longitude: lngStr,
    }));
  };

  const uploadImagesToSupabase = async () => {
    if (!imageFiles.length) return [];

    const uploadedUrls = [];

    for (const file of imageFiles) {
      try {
        const ext = file.name.split('.').pop()?.toLowerCase() || 'jpg';
        const filePath = `${currentUser.id}/${Date.now()}-${Math.random()
          .toString(36)
          .slice(2)}.${ext}`;

        //  Use your actual bucket name here
        const { error: uploadError } = await supabase.storage
          .from('listings-bucket')
          .upload(filePath, file);

        if (uploadError) {
          console.error(
            'Supabase storage upload error for file:',
            file.name,
            uploadError
          );
          throw new Error(
            uploadError.message ||
              `Upload failed for "${file.name}". Check bucket policies and file size.`
          );
        }

        const { data: publicData, error: publicUrlError } =
          supabase.storage.from('listings-bucket').getPublicUrl(filePath);

        if (publicUrlError) {
          console.error(
            'Supabase getPublicUrl error for file:',
            file.name,
            publicUrlError
          );
          throw new Error(
            publicUrlError.message ||
              `Could not get public URL for "${file.name}".`
          );
        }

        uploadedUrls.push(publicData.publicUrl);
      } catch (err) {
        console.error('Image upload failed for file:', file.name, err);
        throw new Error(
          err?.message ||
            `Image upload failed for "${file.name}". Check your Supabase Storage bucket and file size.`
        );
      }
    }

    return uploadedUrls;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setErrorMsg('');
    setSubmitting(true);

    try {
      if (!formData.title.trim() || !formData.location.trim()) {
        throw new Error('Title and location are required.');
      }
      if (!formData.price || isNaN(Number(formData.price))) {
        throw new Error('Please enter a valid price.');
      }

      console.log('Uploading images...');
      const imageUrls = await uploadImagesToSupabase();
      console.log('Image URLs uploaded:', imageUrls);

      const mainImageUrl = imageUrls[0] || formData.img || '';
      console.log('Main Image URL:', mainImageUrl);

      console.log('Calling addListing...');
      const newListing = await addListing({
        ...formData,
        img: mainImageUrl,
        images: imageUrls,
      });
      console.log('New listing added:', newListing);

      setFormData({
        title: '',
        description: '',
        location: '',
        distance: '',
        price: '',
        roomType: 'single',
        gender: 'any',
        amenities: [],
        img: '',
        latitude: '',
        longitude: '',
        landlordContact: '',
      });
      setImageFiles([]);

      if (newListing?.id) {
        history.push(`/listing/${newListing.id}`);
      } else {
        history.push('/dashboard');
      }
    } catch (error) {
      console.error('AddListing submit error:', error);
      setErrorMsg(
        error?.message || 'Failed to add listing. Please try again.'
      );
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <div className="addListing">
      <div className="addListing__inner">
        <div className="addListing__header">
          <h1>Add a new accommodation</h1>
          <p>
            Fill in the details of your listing. Boarders will be able to see your
            photos, description, and contact information.
          </p>
        </div>

        <form className="addListing__form" onSubmit={handleSubmit}>
          <div className="addListing__grid">
            {/* Left column */}
            <div className="addListing__column">
              <TextField
                label="Title"
                name="title"
                value={formData.title}
                onChange={handleChange}
                fullWidth
                margin="normal"
                required
              />

              <TextField
                label="Location (e.g. near UC Banilad)"
                name="location"
                value={formData.location}
                onChange={handleChange}
                fullWidth
                margin="normal"
                required
              />

              <TextField
                label="Distance from campus (optional)"
                name="distance"
                value={formData.distance}
                onChange={handleChange}
                fullWidth
                margin="normal"
                placeholder="e.g. 5-minute walk, 1 jeepney ride"
              />

              <TextField
                label="Monthly price ()"
                name="price"
                type="number"
                value={formData.price}
                onChange={handleChange}
                fullWidth
                margin="normal"
                required
              />

              <FormControl fullWidth margin="normal">
                <InputLabel id="roomType-label">Room type</InputLabel>
                <Select
                  labelId="roomType-label"
                  label="Room type"
                  name="roomType"
                  value={formData.roomType}
                  onChange={handleChange}
                >
                  {roomTypes.map((rt) => (
                    <MenuItem key={rt.value} value={rt.value}>
                      {rt.label}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>

              <FormControl fullWidth margin="normal">
                <InputLabel id="gender-label">Preferred boarder</InputLabel>
                <Select
                  labelId="gender-label"
                  label="Preferred boarder"
                  name="gender"
                  value={formData.gender}
                  onChange={handleChange}
                >
                  {genders.map((g) => (
                    <MenuItem key={g.value} value={g.value}>
                      {g.label}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>

              <FormControl fullWidth margin="normal">
                <InputLabel id="amenities-label">Amenities</InputLabel>
                <Select
                  labelId="amenities-label"
                  multiple
                  name="amenities"
                  value={formData.amenities}
                  onChange={handleAmenityChange}
                  input={<Input />}
                  renderValue={(selected) => (
                    <div
                      style={{
                        display: 'flex',
                        flexWrap: 'wrap',
                        gap: 5,
                      }}
                    >
                      {selected.map((value) => (
                        <Chip key={value} label={value} size="small" />
                      ))}
                    </div>
                  )}
                >
                  {availableAmenities.map((amenity) => (
                    <MenuItem key={amenity} value={amenity}>
                      {amenity}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </div>

            {/* Right column */}
            <div className="addListing__column">
              <TextField
                label="Description"
                name="description"
                value={formData.description}
                onChange={handleChange}
                fullWidth
                margin="normal"
                multiline
                minRows={4}
                placeholder="Describe the room, rules, nearby places, and what makes it good for boarders."
              />

              <TextField
                label="Landlord contact (phone, FB, etc.)"
                name="landlordContact"
                value={formData.landlordContact}
                onChange={handleChange}
                fullWidth
                margin="normal"
                placeholder="This will be visible to boarders."
              />

              <div className="addListing__section">
                <h3>Listing photos</h3>
                <p className="addListing__hint">
                  Upload 15 clear photos of the room and common areas. The first image
                  will be used as the cover.
                </p>

                <input
                  type="file"
                  accept="image/*"
                  multiple
                  onChange={handleImageChange}
                />

                {imageFiles.length > 0 && (
                  <p className="addListing__hint">
                    Selected images: {imageFiles.map((f) => f.name).join(', ')}
                  </p>
                )}

                <p className="addListing__hint" style={{ marginTop: 8 }}>
                  Optional: if you already host images somewhere else, you can also paste
                  a direct image URL here. This will be used as a fallback cover image.
                </p>

                <TextField
                  label="Image URL (optional)"
                  name="img"
                  value={formData.img}
                  onChange={handleChange}
                  fullWidth
                  margin="normal"
                  placeholder="https://example.com/your-image.jpg"
                />
              </div>

              <div className="addListing__section">
                <h3>Location coordinates (optional)</h3>
                <p className="addListing__hint">
                  You can type the latitude/longitude or drag the pin on the map below.
                  The fields will update automatically.
                </p>
                <div className="addListing__coords">
                  <TextField
                    label="Latitude"
                    name="latitude"
                    value={formData.latitude}
                    onChange={handleChange}
                    margin="normal"
                    fullWidth
                  />
                  <TextField
                    label="Longitude"
                    name="longitude"
                    value={formData.longitude}
                    onChange={handleChange}
                    margin="normal"
                    fullWidth
                  />
                </div>
              </div>

              {/* Map preview + pinning using LocationPickerMap */}
              <div className="addListing__section">
                <h3>Map preview</h3>
                <p className="addListing__hint">
                  Drag the pin to the exact location of your accommodation. This helps
                  boarders understand where it is located.
                </p>
                <div className="addListing__mapWrapper">
                  <LocationPickerMap
                    latitude={
                      formData.latitude !== ''
                        ? Number(formData.latitude)
                        : undefined
                    }
                    longitude={
                      formData.longitude !== ''
                        ? Number(formData.longitude)
                        : undefined
                    }
                    onChange={handleMapLocationChange}
                    height={260}
                    zoom={14}
                  />
                </div>
              </div>
            </div>
          </div>

          {errorMsg && (
            <p className="addListing__error">
              {errorMsg}
            </p>
          )}

          <div className="addListing__actions">
            <Button
              variant="outlined"
              onClick={() => history.push('/dashboard')}
              disabled={submitting}
            >
              Cancel
            </Button>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              disabled={submitting}
            >
              {submitting ? <CircularProgress size={20} /> : 'Save listing'}
            </Button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default AddListing;
