import React, { useEffect, useState } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { useApp } from './AppContext';
import './AddListing.css'; // Reuse the same CSS
import { Button, TextField, Select, MenuItem, FormControl, InputLabel, Chip, Input, CircularProgress } from '@mui/material';
import LocationPickerMap from './LocationPickerMap';

function EditListing() {
  const { id } = useParams();
  const history = useHistory();

  const { currentUser, listings, fetchListings, updateListing, loading: authLoading } = useApp();

  const [pageLoading, setPageLoading] = useState(true);
  const [saving, setSaving] = useState(false);

  const [formData, setFormData] = useState({
    title: '',
    location: '',
    distance: '',
    description: '',
    img: '',
    price: '',
    roomType: 'single',
    gender: 'any',
    amenities: [],
    landlordContact: '',
    latitude: 17.618,
    longitude: 121.732,
    available: true
  });

  const availableAmenities = [
    'WiFi',
    'Air Conditioning',
    'Study Desk',
    'Laundry',
    'Kitchen',
    'Private Bathroom',
    'Parking',
    'Security',
    'Meals Included',
    'Gym',
    'Study Area',
    'Pet-Friendly',
    'Utilities Included'
  ];

  // Guard: must be logged in as landlord
  useEffect(() => {
    if (authLoading) return;

    if (!currentUser) {
      history.push('/login');
      return;
    }

    if (currentUser.user_type !== 'landlord') {
      alert('Only landlords can edit listings');
      history.push('/');
    }
  }, [authLoading, currentUser, history]);

  // Load listing (wait for listings to be available)
  useEffect(() => {
    let active = true;

    const load = async () => {
      if (authLoading) return;
      if (!currentUser) return;

      setPageLoading(true);

      try {
        let data = listings;

        // If listings not loaded yet, fetch them
        if (!data || data.length === 0) {
          data = await fetchListings();
        }

        const listing = (data || []).find((l) => l.id === id);

        if (!listing) {
          alert('Listing not found');
          history.push('/dashboard');
          return;
        }

        // Check ownership
        if (listing.landlord_id !== currentUser.id) {
          alert('You can only edit your own listings');
          history.push('/dashboard');
          return;
        }

        if (!active) return;

        setFormData({
          title: listing.title || '',
          location: listing.location || '',
          distance: listing.distance || '',
          description: listing.description || '',
          img: listing.img || '',
          price: listing.price !== null && listing.price !== undefined ? String(listing.price) : '',
          roomType: listing.room_type || 'single',
          gender: listing.gender || 'any',
          amenities: listing.amenities || [],
          landlordContact: listing.landlord_contact || '',
          latitude: listing.latitude !== null && listing.latitude !== undefined ? Number(listing.latitude) : 17.618,
          longitude: listing.longitude !== null && listing.longitude !== undefined ? Number(listing.longitude) : 121.732,
          available: listing.available !== false
        });
      } catch (error) {
        console.error('EditListing load error:', error);
        alert('Error loading listing: ' + (error?.message || 'Unknown error'));
        history.push('/dashboard');
      } finally {
        if (active) setPageLoading(false);
      }
    };

    load();

    return () => {
      active = false;
    };
  }, [id, listings, fetchListings, currentUser, history, authLoading]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleAmenityChange = (e) => {
    setFormData((prev) => ({ ...prev, amenities: e.target.value }));
  };

  const handleCoordinateChange = (e) => {
    const { name, value } = e.target;

    if (value === '') {
      setFormData((prev) => ({ ...prev, [name]: '' }));
      return;
    }

    const num = parseFloat(value);
    setFormData((prev) => ({
      ...prev,
      [name]: Number.isFinite(num) ? num : prev[name]
    }));
  };

  const handleMapChange = ({ latitude, longitude }) => {
    setFormData((prev) => ({ ...prev, latitude, longitude }));
  };

  const handleAvailabilityChange = (e) => {
    setFormData((prev) => ({ ...prev, available: e.target.value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSaving(true);

    try {
      const parsedPrice = parseFloat(formData.price);
      const parsedLat = parseFloat(formData.latitude);
      const parsedLng = parseFloat(formData.longitude);

      if (!Number.isFinite(parsedPrice)) {
        alert('Please enter a valid price');
        return;
      }

      if (!Number.isFinite(parsedLat) || !Number.isFinite(parsedLng)) {
        alert('Please enter valid latitude and longitude (or pin the location on the map)');
        return;
      }

      const updates = {
        title: formData.title,
        location: formData.location,
        distance: formData.distance,
        description: formData.description,
        img: formData.img || 'https://images.unsplash.com/photo-1560185127-6ed189bf02f4?w=600',
        price: parsedPrice,
        room_type: formData.roomType,
        gender: formData.gender,
        amenities: formData.amenities,
        landlord_contact: formData.landlordContact,
        latitude: parsedLat,
        longitude: parsedLng,
        available: formData.available
      };

      await updateListing(id, updates);
      alert('Listing updated successfully!');
      history.push('/dashboard');
    } catch (error) {
      console.error('EditListing save error:', error);
      alert('Error updating listing: ' + (error?.message || 'Unknown error'));
    } finally {
      setSaving(false);
    }
  };

  if (authLoading || pageLoading) {
    return (
      <div className="addListing">
        <div className="addListing__container" style={{ display: 'flex', justifyContent: 'center', padding: 40 }}>
          <CircularProgress />
        </div>
      </div>
    );
  }

  return (
    <div className="addListing">
      <div className="addListing__container">
        <h2>Edit Listing</h2>

        <form onSubmit={handleSubmit}>
          <TextField
            label="Property Title"
            name="title"
            value={formData.title}
            onChange={handleChange}
            fullWidth
            required
            margin="normal"
            variant="outlined"
            placeholder="e.g., Modern Single Room near Campus"
          />

          <TextField
            label="Location"
            name="location"
            value={formData.location}
            onChange={handleChange}
            fullWidth
            required
            margin="normal"
            variant="outlined"
            placeholder="e.g., Near CSU Carig Campus"
          />

          <TextField
            label="Distance from University"
            name="distance"
            value={formData.distance}
            onChange={handleChange}
            fullWidth
            margin="normal"
            variant="outlined"
            placeholder="e.g., 5 mins walk"
          />

          <TextField
            label="Description"
            name="description"
            value={formData.description}
            onChange={handleChange}
            fullWidth
            required
            multiline
            minRows={4}
            margin="normal"
            variant="outlined"
            placeholder="Describe your property..."
          />

          <TextField
            label="Image URL (optional)"
            name="img"
            value={formData.img}
            onChange={handleChange}
            fullWidth
            margin="normal"
            variant="outlined"
            placeholder="https://example.com/image.jpg"
          />

          <TextField
            label="Monthly Rent ()"
            name="price"
            type="number"
            value={formData.price}
            onChange={handleChange}
            fullWidth
            required
            margin="normal"
            variant="outlined"
            placeholder="e.g., 3000"
          />

          <FormControl fullWidth margin="normal" variant="outlined">
            <InputLabel>Room Type</InputLabel>
            <Select name="roomType" value={formData.roomType} onChange={handleChange} label="Room Type">
              <MenuItem value="single">Single Room</MenuItem>
              <MenuItem value="shared">Shared Room</MenuItem>
              <MenuItem value="studio">Studio</MenuItem>
              <MenuItem value="apartment">Apartment</MenuItem>
            </Select>
          </FormControl>

          <FormControl fullWidth margin="normal" variant="outlined">
            <InputLabel>Gender Preference</InputLabel>
            <Select name="gender" value={formData.gender} onChange={handleChange} label="Gender Preference">
              <MenuItem value="any">Any</MenuItem>
              <MenuItem value="male">Male Only</MenuItem>
              <MenuItem value="female">Female Only</MenuItem>
            </Select>
          </FormControl>

          <FormControl fullWidth margin="normal" variant="outlined">
            <InputLabel>Amenities</InputLabel>
            <Select
              multiple
              name="amenities"
              value={formData.amenities}
              onChange={handleAmenityChange}
              input={<Input />}
              renderValue={(selected) => (
                <div style={{ display: 'flex', flexWrap: 'wrap', gap: 5 }}>
                  {selected.map((value) => (
                    <Chip key={value} label={value} size="small" />
                  ))}
                </div>
              )}
            >
              {availableAmenities.map((amenity) => (
                <MenuItem key={amenity} value={amenity}>
                  {amenity}
                </MenuItem>
              ))}
            </Select>
          </FormControl>

          <TextField
            label="Contact Number"
            name="landlordContact"
            value={formData.landlordContact}
            onChange={handleChange}
            fullWidth
            required
            margin="normal"
            variant="outlined"
            placeholder="e.g., 09123456789"
          />

          <FormControl fullWidth margin="normal" variant="outlined">
            <InputLabel>Availability</InputLabel>
            <Select name="available" value={formData.available} onChange={handleAvailabilityChange} label="Availability">
              <MenuItem value={true}>Available</MenuItem>
              <MenuItem value={false}>Occupied/Unavailable</MenuItem>
            </Select>
          </FormControl>

          <div className="location-picker">
            <h3 style={{ marginTop: 0 }}>Pin Location on Map</h3>
            <p style={{ marginBottom: 12, color: '#666' }}>Drag the marker to adjust the location.</p>

            <div className="add-listing-map">
              <LocationPickerMap
                latitude={formData.latitude}
                longitude={formData.longitude}
                onChange={handleMapChange}
                height={400}
                zoom={14}
              />
            </div>

            <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: 15, marginTop: 15 }}>
              <TextField
                label="Latitude"
                name="latitude"
                type="number"
                value={formData.latitude}
                onChange={handleCoordinateChange}
                inputProps={{ step: 0.0001 }}
                variant="outlined"
                size="small"
              />
              <TextField
                label="Longitude"
                name="longitude"
                type="number"
                value={formData.longitude}
                onChange={handleCoordinateChange}
                inputProps={{ step: 0.0001 }}
                variant="outlined"
                size="small"
              />
            </div>
          </div>

          <div className="addListing__actions">
            <Button
              variant="outlined"
              onClick={() => history.push('/dashboard')}
              style={{ borderColor: '#999', color: '#999' }}
              disabled={saving}
            >
              Cancel
            </Button>

            <Button type="submit" variant="contained" disabled={saving} style={{ backgroundColor: '#3498db', color: 'white' }}>
              {saving ? 'Saving' : 'Save Changes'}
            </Button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default EditListing;
