import React, { useEffect, useRef } from 'react';
import { useApp } from './AppContext';
import './Map.css';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';

// Fix for default marker icons in Leaflet (CRA + Webpack)
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
});

// Real coordinates for popular areas in Tuguegarao (stable constant to satisfy ESLint deps)
const POPULAR_AREAS = [
  { id: 1, name: 'Cagayan State University - Carig Campus', lat: 17.65959795257981, lng: 121.7520532778682, type: 'university' },
  { id: 2, name: 'Cagayan State University - Andrews Campus', lat: 17.61975636903237, lng: 121.72511110848909, type: 'university' },
  { id: 3, name: 'University of Saint Louis - BCJ Campus', lat: 17.643793695592557, lng: 121.74080911217742, type: 'university' },
  { id: 4, name: 'University of Saint Louis - Main Campus', lat: 17.61048425025793, lng: 121.72427197319682, type: 'university' },
  { id: 5, name: 'Saint Paul University of the Philippines', lat: 17.617090360605125, lng: 121.7237176817696, type: 'university' },
  { id: 6, name: 'University of Cagayan Valley', lat: 17.623874295239744, lng: 121.73045919850294, type: 'university' },
  { id: 7, name: 'SM City Tuguegarao', lat: 17.627532401895664, lng: 121.7183575720471, type: 'shopping' },
  { id: 8, name: "Robinson's Place Tuguegarao", lat: 17.627245259376725, lng: 121.73315442500986, type: 'shopping' },
  { id: 9, name: 'SM Downtown Tuguegarao', lat: 17.612921406477405, lng: 121.72382768760724, type: 'shopping' }
];
 
const createEmojiIcon = (emoji, type) =>
  L.divIcon({
    className: `custom-marker ${type}`,
    html: `<div class="marker-pin ${type}-pin">${emoji}</div>`,
    iconSize: [40, 40],
    iconAnchor: [20, 40]
  });

function Map() {
  const { listings } = useApp();
  const mapRef = useRef(null);
  const mapInstanceRef = useRef(null);
  const listingsLayerRef = useRef(null);

  // Icons stored in refs so we don't recreate or fight ESLint deps
  const iconsRef = useRef({
    universityIcon: null,
    shoppingIcon: null,
    listingIcon: null
  });

  // Initialize the map ONCE
  useEffect(() => {
    if (mapInstanceRef.current || !mapRef.current) return;

    try {
      const map = L.map(mapRef.current).setView([17.618, 121.732], 13);

      L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
        attribution: ' OpenStreetMap contributors',
        maxZoom: 19
      }).addTo(map);

      // Create & store icons
      iconsRef.current.universityIcon = createEmojiIcon('', 'university');
      iconsRef.current.shoppingIcon = createEmojiIcon('', 'shopping');
      iconsRef.current.listingIcon = createEmojiIcon('', 'listing');

      // Add popular area markers once
      POPULAR_AREAS.forEach((area) => {
        const icon = area.type === 'university' ? iconsRef.current.universityIcon : iconsRef.current.shoppingIcon;
        const marker = L.marker([area.lat, area.lng], { icon }).addTo(map);

        marker.bindPopup(`
          <div class="map-popup">
            <h4>${area.name}</h4>
            <p><strong>Type:</strong> ${area.type === 'university' ? 'University/Campus' : 'Shopping Center'}</p>
          </div>
        `);
      });

      // Layer group for listing markers (updated whenever listings changes)
      listingsLayerRef.current = L.layerGroup().addTo(map);

      mapInstanceRef.current = map;
    } catch (error) {
      console.error('Map initialization error:', error);
    }

    return () => {
      try {
        if (mapInstanceRef.current) {
          mapInstanceRef.current.remove();
          mapInstanceRef.current = null;
          listingsLayerRef.current = null;
        }
      } catch (err) {
        console.error('Map cleanup error:', err);
      }
    };
  }, []);

  // Update listing markers whenever listings changes (without reinitializing the map)
  useEffect(() => {
    if (!mapInstanceRef.current || !listingsLayerRef.current) return;

    try {
      listingsLayerRef.current.clearLayers();

      if (!listings || listings.length === 0) return;

      listings.forEach((listing) => {
        const lat = Number(listing.latitude);
        const lng = Number(listing.longitude);

        if (!Number.isFinite(lat) || !Number.isFinite(lng)) return;

        const marker = L.marker([lat, lng], { icon: iconsRef.current.listingIcon }).addTo(listingsLayerRef.current);

        const priceText =
          listing.price !== null && listing.price !== undefined ? `${Number(listing.price).toLocaleString()}/month` : '0/month';

        marker.bindPopup(`
          <div class="map-popup listing-popup">
            <h4>${listing.title || 'Listing'}</h4>
            <p><strong>Location:</strong> ${listing.location || ''}</p>
            <p><strong>Price:</strong> ${priceText}</p>
            <p class="status ${listing.available ? 'available-status' : 'unavailable-status'}">
              ${listing.available ? ' Available' : ' Occupied'}
            </p>
          </div>
        `);
      });
    } catch (error) {
      console.error('Map listings update error:', error);
    }
  }, [listings]);

  return (
    <div className="map-container">
      <div className="map-header">
        <h2>Explore Tuguegarao Housing Locations</h2>
        <p>View popular areas and available student accommodations on the interactive map</p>
      </div>
      <div ref={mapRef} className="map" id="main-map"></div>
    </div>
  );
}

export default Map;
