import React, { useEffect, useState } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import './ListingDetail.css';
import { useApp } from './AppContext';
import {
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Chip,
  CircularProgress,
} from '@mui/material';

function ListingDetail() {
  const { id } = useParams();
  const history = useHistory();

  const {
    listings,
    fetchListings,
    isAuthenticated,
    currentUser,
    canBookListing,
    canEditListing,
    createBooking,
  } = useApp();

  const [listing, setListing] = useState(null);
  const [loading, setLoading] = useState(true);

  const [bookingDialogOpen, setBookingDialogOpen] = useState(false);
  const [bookingDate, setBookingDate] = useState('');
  const [visitationDate, setVisitationDate] = useState('');
  const [bookingNotes, setBookingNotes] = useState('');
  const [bookingSubmitting, setBookingSubmitting] = useState(false);

  useEffect(() => {
    const loadListing = async () => {
      setLoading(true);

      let found = listings.find((l) => l.id === id);
      if (!found) {
        const data = await fetchListings();
        found = data.find((l) => l.id === id) || null;
      }

      setListing(found);
      setLoading(false);
    };

    loadListing();
  }, [id, listings, fetchListings]);

  if (loading) {
    return (
      <div className="listingDetail listingDetail--center">
        <CircularProgress />
      </div>
    );
  }

  if (!listing) {
    return (
      <div className="listingDetail listingDetail--center">
        <p>Listing not found.</p>
        <Button variant="outlined" onClick={() => history.push('/search')}>
          Back to search
        </Button>
      </div>
    );
  }

  const isMyListing = currentUser && listing.landlord_id === currentUser.id;
  const canBook = canBookListing(listing.id);
  const canEdit = canEditListing(listing.id);

  const handleOpenBookingDialog = () => {
    if (!isAuthenticated) {
      history.push('/login');
      return;
    }
    if (!canBook) {
      alert(
        'You cannot book this listing (you might be the landlord, or not a boarder).'
      );
      return;
    }
    setBookingDialogOpen(true);
  };

  const handleCloseBookingDialog = () => {
    if (bookingSubmitting) return;
    setBookingDialogOpen(false);
  };

  const handleConfirmBooking = async () => {
    if (!bookingDate) {
      alert('Please select a move-in date.');
      return;
    }
    try {
      setBookingSubmitting(true);

      const booking = await createBooking(
        listing.id,
        bookingDate,
        visitationDate,
        bookingNotes
      );

      if (!booking || !booking.id) {
        throw new Error('Booking was created but no booking ID was returned.');
      }

      setBookingDialogOpen(false);
      setBookingDate('');
      setVisitationDate('');
      setBookingNotes('');

      alert(
        'Booking request sent! Opening chat so you can confirm details with the landlord.'
      );

      history.push(`/bookings/${booking.id}/chat`);
    } catch (error) {
      console.error('createBooking error:', error);
      alert(error?.message || 'Failed to create booking. Please try again.');
    } finally {
      setBookingSubmitting(false);
    }
  };

  return (
    <div className="listingDetail">
      {/* Top bar */}
      <div className="listingDetail__topBar">
        <Button variant="text" onClick={() => history.goBack()}>
           Back to results
        </Button>
      </div>

      {/* Main container */}
      <div className="listingDetail__container">
        {/* Left: images + description */}
        <div className="listingDetail__left">
          <div className="listingDetail__titleBlock">
            <h1 className="listingDetail__title">{listing.title}</h1>
            <p className="listingDetail__location">{listing.location}</p>

            <div className="listingDetail__metaRow">
              <span className="listingDetail__metaPill">
                {listing.room_type &&
                  listing.room_type.charAt(0).toUpperCase() +
                    listing.room_type.slice(1)}{' '}
                room
              </span>
              {listing.gender && (
                <span className="listingDetail__metaPill">
                  Preferred:{' '}
                  {listing.gender === 'any' ? 'Any gender' : listing.gender}
                </span>
              )}
              {listing.distance && (
                <span className="listingDetail__metaPill">
                  {listing.distance}
                </span>
              )}
            </div>
          </div>

          {listing.img && (
            <div className="listingDetail__imageGrid">
              <div className="listingDetail__imageMainWrapper">
                <img
                  src={listing.img}
                  alt={listing.title}
                  className="listingDetail__imageMain"
                />
              </div>
              {/* If you later add multiple images, you can show thumbnails here */}
            </div>
          )}

          <section className="listingDetail__section">
            <h2 className="listingDetail__sectionTitle">About this place</h2>
            <p className="listingDetail__description">{listing.description}</p>
          </section>

          {listing.amenities && listing.amenities.length > 0 && (
            <section className="listingDetail__section">
              <h2 className="listingDetail__sectionTitle">Amenities</h2>
              <div className="listingDetail__amenities">
                {listing.amenities.map((a) => (
                  <Chip
                    key={a}
                    label={a}
                    className="listingDetail__amenityChip"
                  />
                ))}
              </div>
            </section>
          )}
        </div>

        {/* Right: price + host + booking card */}
        <aside className="listingDetail__right">
          <div className="listingDetail__bookingCard">
            <div className="listingDetail__priceRow">
              <div>
                <span className="listingDetail__price">
                  {Number(listing.price || 0).toLocaleString()}
                </span>
                <span className="listingDetail__priceUnit"> / month</span>
              </div>
            </div>

            <div className="listingDetail__hostBlock">
              <p className="listingDetail__hostLabel">Hosted by</p>
              <p className="listingDetail__hostName">
                {listing.landlord_name || 'Landlord'}
              </p>
              {listing.landlord_contact && (
                <p className="listingDetail__hostContact">
                  Contact: {listing.landlord_contact}
                </p>
              )}
            </div>

            <div className="listingDetail__divider" />

            <div className="listingDetail__actions">
              {isMyListing && canEdit && (
                <Button
                  variant="outlined"
                  fullWidth
                  onClick={() => history.push(`/edit-listing/${listing.id}`)}
                  style={{ marginBottom: 8 }}
                >
                  Edit listing
                </Button>
              )}

              {!isMyListing && (
                <Button
                  variant="contained"
                  color="primary"
                  fullWidth
                  onClick={handleOpenBookingDialog}
                >
                  Request booking
                </Button>
              )}
            </div>

            <div className="listingDetail__infoNote">
              <p>
                Boarders can propose a{' '}
                <strong>visitation date and move-in date</strong>. Use the chat after
                booking to confirm details and house rules.
              </p>
            </div>
          </div>
        </aside>
      </div>

      {/* Booking dialog */}
      <Dialog
        open={bookingDialogOpen}
        onClose={handleCloseBookingDialog}
        fullWidth
        maxWidth="sm"
      >
        <DialogTitle>Request a booking</DialogTitle>
        <DialogContent>
          <p>
            You are requesting <strong>{listing.title}</strong> in{' '}
            <strong>{listing.location}</strong>.
          </p>

          <TextField
            label="Preferred move-in date"
            type="date"
            value={bookingDate}
            onChange={(e) => setBookingDate(e.target.value)}
            fullWidth
            required
            margin="normal"
            InputLabelProps={{
              shrink: true,
            }}
          />

          <TextField
            label="Preferred visitation date (optional)"
            type="date"
            value={visitationDate}
            onChange={(e) => setVisitationDate(e.target.value)}
            fullWidth
            margin="normal"
            InputLabelProps={{
              shrink: true,
            }}
          />

          <TextField
            label="Message / Notes to landlord"
            value={bookingNotes}
            onChange={(e) => setBookingNotes(e.target.value)}
            fullWidth
            margin="normal"
            multiline
            minRows={3}
            variant="outlined"
            placeholder="Introduce yourself, your preferred schedule, and any questions about the room, visitation, or dates."
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseBookingDialog} disabled={bookingSubmitting}>
            Cancel
          </Button>
          <Button
            onClick={handleConfirmBooking}
            variant="contained"
            color="primary"
            disabled={bookingSubmitting}
          >
            {bookingSubmitting ? <CircularProgress size={20} /> : 'Send request'}
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default ListingDetail;
