import React, { useEffect, useMemo, useState } from 'react';
import { useApp } from './AppContext';
import { useHistory } from 'react-router-dom';
import './Auth.css';
import {
  Button,
  TextField,
  Radio,
  RadioGroup,
  FormControlLabel,
  FormControl,
  FormLabel,
  CircularProgress,
  Alert,
  Snackbar,
  Checkbox,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Typography,
} from '@mui/material';

// Helper: prevent hanging forever on a bad network / config
const withTimeout = (promise, ms) =>
  Promise.race([
    promise,
    new Promise((_, reject) =>
      setTimeout(
        () =>
          reject(
            new Error(
              'Login request took too long. This usually means Supabase is not responding. ' +
              'Check your internet connection and your Supabase .env configuration.'
            )
          ),
        ms
      )
    ),
  ]);

function Auth() {
  const { login, signup, isAuthenticated, currentUser } = useApp();
  const history = useHistory();

  const [isLogin, setIsLogin] = useState(true);
  const [submitting, setSubmitting] = useState(false);

  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  const [formData, setFormData] = useState({
    email: '',
    password: '',
    name: '',
    userType: 'student',
  });

  // Terms & Conditions popup for sign-up
  const [acceptedTerms, setAcceptedTerms] = useState(false);
  const [termsOpen, setTermsOpen] = useState(false);

  // If already logged in, redirect away from auth page
  useEffect(() => {
    if (isAuthenticated && currentUser) {
      history.replace('/dashboard');
    }
  }, [isAuthenticated, currentUser, history]);

  const parsedErrorMessage = useMemo(() => error || '', [error]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    setSubmitting(true);

    const email = (formData.email || '').trim();
    const password = formData.password || '';
    const name = (formData.name || '').trim();
    const userType = formData.userType || 'student';

    console.log('Auth: Form submission started', { isLogin, email });

    try {
      if (isLogin) {
        console.log('Auth: Attempting login...');

        // wrap login in a timeout so it cant hang forever
        const user = await withTimeout(login(email, password), 15000);

        if (!user) {
          throw new Error('Login failed. Please check your credentials and try again.');
        }

        console.log('Auth: Login successful', user?.email);
        setSuccess('Login successful! Redirecting...');
        history.replace('/dashboard');
        return;
      }

      // SIGNUP
      console.log('Auth: Attempting signup...');
      const result = await withTimeout(
        signup(email, password, name, userType),
        20000
      );

      if (result && result.pendingConfirmation) {
        setSuccess(
          'Account created! Please check your email to confirm your account, then log in.'
        );
        setIsLogin(true);
        setSubmitting(false);
        return;
      }

      console.log('Auth: Signup successful', result?.email);
      setSuccess('Account created successfully! Redirecting...');
      history.replace('/dashboard');
    } catch (err) {
      console.error('Auth: Error caught:', err);
      const msg = err?.message ? err.message : 'An error occurred. Please try again.';

      let errorMessage = msg;

      if (msg.includes('Invalid login credentials')) {
        errorMessage = 'Invalid email or password.';
      } else if (msg.includes('already registered')) {
        errorMessage = 'Email already registered. Please login.';
      } else if (
        msg.includes('Password should be at least') ||
        msg.includes('at least 6 characters')
      ) {
        errorMessage = 'Password must be at least 6 characters.';
      } else if (msg.includes('Email not confirmed')) {
        errorMessage = 'Please check your email to confirm your account.';
      } else if (msg.toLowerCase().includes('rate limit')) {
        errorMessage = 'Too many attempts. Please wait a moment and try again.';
      } else if (msg.toLowerCase().includes('timed out')) {
        errorMessage =
          'Login request timed out. This means Supabase did not respond in time. ' +
          'Double-check your .env (URL and anon key) and restart `npm start`.';
      } else if (msg.toLowerCase().includes('failed to fetch')) {
        errorMessage =
          'Network error (Failed to fetch). Check your internet connection and that REACT_APP_SUPABASE_URL is correct in .env.';
      }

      setError(errorMessage);
    } finally {
      setSubmitting(false);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;

    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const isFormValid = () => {
    if (!formData.email || !formData.password) return false;
    if (!isLogin && !formData.name) return false;
    if (formData.password.length < 6) return false;
    if (!isLogin && !acceptedTerms) return false;
    return true;
  };

  return (
    <div className="auth">
      <div className="auth__container">
        <h2>{isLogin ? 'Login to DormFinds' : 'Create Your Account'}</h2>
        <p className="auth__subtitle">
          {isLogin
            ? 'Welcome back! Please login to continue.'
            : 'Join DormFinds to find or list boarder-friendly accommodations.'}
        </p>

        <Snackbar
          open={!!parsedErrorMessage}
          autoHideDuration={8000}
          onClose={() => setError('')}
          anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
        >
          <Alert
            onClose={() => setError('')}
            severity="error"
            sx={{ width: '100%', whiteSpace: 'pre-line' }}
          >
            {parsedErrorMessage}
          </Alert>
        </Snackbar>

        <Snackbar
          open={!!success}
          autoHideDuration={6000}
          onClose={() => setSuccess('')}
          anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
        >
          <Alert onClose={() => setSuccess('')} severity="success" sx={{ width: '100%' }}>
            {success}
          </Alert>
        </Snackbar>

        <form onSubmit={handleSubmit}>
          {!isLogin && (
            <TextField
              label="Full Name"
              name="name"
              value={formData.name}
              onChange={handleChange}
              fullWidth
              required
              margin="normal"
              variant="outlined"
              disabled={submitting}
            />
          )}

          <TextField
            label="Email"
            name="email"
            type="email"
            value={formData.email}
            onChange={handleChange}
            fullWidth
            required
            margin="normal"
            variant="outlined"
            disabled={submitting}
          />

          <TextField
            label="Password"
            name="password"
            type="password"
            value={formData.password}
            onChange={handleChange}
            fullWidth
            required
            margin="normal"
            variant="outlined"
            disabled={submitting}
            helperText="Minimum 6 characters"
          />

          {!isLogin && (
            <>
              <FormControl
                component="fieldset"
                margin="normal"
                disabled={submitting}
                className="auth__usertype"
              >
                <FormLabel component="legend">I am a:</FormLabel>
                <RadioGroup
                  name="userType"
                  value={formData.userType}
                  onChange={handleChange}
                  row
                >
                  <FormControlLabel value="student" control={<Radio />} label="Boarder" />
                  <FormControlLabel
                    value="landlord"
                    control={<Radio />}
                    label="Property Owner"
                  />
                </RadioGroup>
              </FormControl>

              {/* Terms & Conditions checkbox + popup link */}
              <div style={{ marginTop: 8 }}>
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={acceptedTerms}
                      onChange={(e) => setAcceptedTerms(e.target.checked)}
                      color="primary"
                    />
                  }
                  label={
                    <span>
                      I have read and agree to the{' '}
                      <button
                        type="button"
                        onClick={() => setTermsOpen(true)}
                        style={{
                          background: 'none',
                          border: 'none',
                          padding: 0,
                          margin: 0,
                          color: '#3498db',
                          cursor: 'pointer',
                          textDecoration: 'underline',
                          font: 'inherit',
                        }}
                      >
                        Terms &amp; Conditions and Privacy Notice
                      </button>
                      .
                    </span>
                  }
                />
              </div>
            </>
          )}

          <Button
            type="submit"
            variant="contained"
            fullWidth
            disabled={submitting || !isFormValid()}
            className="auth__submit"
          >
            {submitting ? (
              <CircularProgress size={24} sx={{ color: 'white' }} />
            ) : isLogin ? (
              'Login'
            ) : (
              'Sign Up'
            )}
          </Button>
        </form>

        <div className="auth__toggle">
          <p>
            {isLogin ? "Don't have an account? " : 'Already have an account? '}
            <span
              onClick={() => {
                if (submitting) return;
                setIsLogin((v) => !v);
                setError('');
                setSuccess('');
                setAcceptedTerms(false);
              }}
              style={{
                cursor: submitting ? 'not-allowed' : 'pointer',
                opacity: submitting ? 0.5 : 1,
                color: '#3498db',
                fontWeight: 600,
              }}
            >
              {isLogin ? 'Sign up' : 'Login'}
            </span>
          </p>
        </div>

        <div className="auth__guest">
          <p style={{ margin: '20px 0 10px', color: '#7f8c8d' }}>Or</p>
          <Button
            variant="outlined"
            fullWidth
            onClick={() => history.push('/')}
            disabled={submitting}
            sx={{
              borderColor: '#3498db',
              color: '#3498db',
              '&:hover': {
                borderColor: '#2980b9',
                backgroundColor: 'rgba(52, 152, 219, 0.04)',
              },
            }}
          >
            Continue as Guest
          </Button>
        </div>

        {/* Terms & Conditions / Privacy Notice dialog */}
        <Dialog
          open={termsOpen}
          onClose={() => setTermsOpen(false)}
          maxWidth="sm"
          fullWidth
        >
          <DialogTitle>Terms &amp; Conditions &amp; Privacy Notice</DialogTitle>
          <DialogContent dividers>
            <Typography variant="body2" gutterBottom>
              By creating an account on DormFinds, you confirm that you are at least 18 years
              old and that all information you provide is true and correct to the best of
              your knowledge.
            </Typography>
            <Typography variant="body2" gutterBottom>
              We collect your name, email address, and (for landlords) business permit
              details for the purpose of matching boarders and property owners, preventing
              abuse, and improving the service. Your data is processed in accordance with
              the Data Privacy Act of 2012 (Republic Act No. 10173) of the Philippines.
            </Typography>
            <Typography variant="body2" gutterBottom>
              DormFinds is a listing and messaging platform only. We do not handle payments,
              deposits, or rent on your behalf. You are solely responsible for verifying the
              identity and legitimacy of other users and any properties or offers.
            </Typography>
            <Typography variant="body2" gutterBottom>
              DormFinds and its developer are not liable for any losses, scams, fraud, or
              disputes that may arise between boarders and landlords, whether online or
              offline. Never send money in advance without proper verification, written
              agreements, and official receipts where applicable.
            </Typography>
            <Typography variant="body2" gutterBottom>
              By proceeding, you consent to the collection and processing of your personal
              data for the purposes stated above and agree to use the platform in a lawful,
              respectful manner. You may request access, correction, or deletion of your
              data by contacting the developer.
            </Typography>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setTermsOpen(false)}>Close</Button>
          </DialogActions>
        </Dialog>
      </div>
    </div>
  );
}

export default Auth;
