// src/Header.js
import React, { useState } from 'react';
import './Header.css';
import { useHistory } from 'react-router-dom';
import { useApp } from './AppContext';
import {
  AppBar,
  Toolbar,
  Button,
  IconButton,
  Menu,
  MenuItem,
  Typography,
  Badge,
  Tooltip,
} from '@mui/material';
import NotificationsIcon from '@mui/icons-material/Notifications';
import AccountCircle from '@mui/icons-material/AccountCircle';

function Header() {
  const history = useHistory();
  const { currentUser, isAuthenticated, logout } = useApp();

  const [userMenuAnchor, setUserMenuAnchor] = useState(null);
  const userMenuOpen = Boolean(userMenuAnchor);

  const isLandlord = currentUser?.user_type === 'landlord';

  const handleLogoClick = () => {
    history.push('/');
  };

  const handleListPropertyClick = () => {
    if (!isLandlord) {
      // Not a landlord yet  go to Apply as Landlord
      history.push('/apply-landlord');
    } else {
      // Already landlord  go directly to Add Listing
      history.push('/add-listing');
    }
  };

  const handleUserMenuOpen = (event) => {
    setUserMenuAnchor(event.currentTarget);
  };

  const handleUserMenuClose = () => {
    setUserMenuAnchor(null);
  };

  const handleLogout = async () => {
    handleUserMenuClose();
    await logout();
    history.push('/');
  };

  const userInitial =
    currentUser?.name?.charAt(0)?.toUpperCase() ||
    currentUser?.email?.charAt(0)?.toUpperCase() ||
    '?';

  const userDisplayName =
    currentUser?.name || currentUser?.email || 'Account';

  return (
    <AppBar position="static" color="default" className="header">
      <Toolbar className="header__toolbar">
        {/* Logo */}
        <div className="header__logo" onClick={handleLogoClick}>
          <div className="header__logoMark">DF</div>
          <div className="header__logoTextBlock">
            <span className="header__logoTitle">DormFinds</span>
            <span className="header__logoSubtitle">Find boarding near your campus</span>
          </div>
        </div>

        {/* Nav */}
        <nav className="header__nav">
          <Button
            size="small"
            onClick={() => history.push('/')}
          >
            Find accommodations
          </Button>

          <Button
            size="small"
            onClick={() => history.push('/search')}
          >
            Search
          </Button>

          {isAuthenticated && (
            <Button
              size="small"
              onClick={() => history.push('/favorites')}
            >
              Saved
            </Button>
          )}

          <Button
            size="small"
            onClick={() => history.push('/support-developer')}
          >
            Support the developer
          </Button>
        </nav>

        {/* Right side */}
        <div className="header__right">
          {isAuthenticated && (
            <Tooltip title="Notifications (bookings & chat)">
              <IconButton className="header__iconButton">
                <Badge badgeContent={0} color="primary">
                  <NotificationsIcon />
                </Badge>
              </IconButton>
            </Tooltip>
          )}

          {/* List your property */}
          <Button
            variant="outlined"
            size="small"
            onClick={handleListPropertyClick}
          >
            List your property
          </Button>

          {/* Auth controls */}
          {!isAuthenticated ? (
            <Button
              size="small"
              onClick={() => history.push('/login')}
            >
              Login
            </Button>
          ) : (
            <>
              <Button
                className="header__userButton"
                onClick={handleUserMenuOpen}
              >
                <span className="header__userInitial">{userInitial}</span>
                <span className="header__userName">{userDisplayName}</span>
              </Button>

              <Menu
                anchorEl={userMenuAnchor}
                open={userMenuOpen}
                onClose={handleUserMenuClose}
                anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                transformOrigin={{ vertical: 'top', horizontal: 'right' }}
              >
                <MenuItem
                  onClick={() => {
                    handleUserMenuClose();
                    history.push('/dashboard');
                  }}
                >
                  <AccountCircle fontSize="small" style={{ marginRight: 8 }} />
                  Dashboard
                </MenuItem>

                {!isLandlord && (
                  <MenuItem
                    onClick={() => {
                      handleUserMenuClose();
                      history.push('/apply-landlord');
                    }}
                  >
                    Apply as landlord
                  </MenuItem>
                )}

                <MenuItem
                  onClick={() => {
                    handleUserMenuClose();
                    history.push('/support-developer');
                  }}
                >
                  Support the developer
                </MenuItem>

                <MenuItem onClick={handleLogout}>Log out</MenuItem>
              </Menu>
            </>
          )}
        </div>
      </Toolbar>
    </AppBar>
  );
}

export default Header;
