import React, { useEffect, useRef, useState } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import './ChatPage.css';
import { useApp } from './AppContext';
import { TextField, IconButton, CircularProgress } from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';

function ChatPage() {
  const { bookingId } = useParams();
  const history = useHistory();
  const {
    currentUser,
    isAuthenticated,
    getBookingById,
    fetchBookings,
    fetchMessagesForBooking,
    getMessagesForBooking,
    sendMessage,
    markMessagesRead,
  } = useApp();

  const [localBooking, setLocalBooking] = useState(null);
  const [loadingBooking, setLoadingBooking] = useState(true);
  const [loadingMessages, setLoadingMessages] = useState(true);
  const [input, setInput] = useState('');
  const [sending, setSending] = useState(false);

  const messagesEndRef = useRef(null);

  const messages = getMessagesForBooking(bookingId);

  // Scroll to bottom when messages change
  useEffect(() => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }
  }, [messages]);

  // Load booking info
  useEffect(() => {
    const loadBooking = async () => {
      if (!isAuthenticated || !currentUser) {
        history.replace('/login');
        return;
      }

      setLoadingBooking(true);

      let booking = getBookingById(bookingId);
      if (!booking) {
        const data = await fetchBookings();
        booking = data.find((b) => b.id === bookingId) || null;
      }

      setLocalBooking(booking || null);
      setLoadingBooking(false);
    };

    loadBooking();
  }, [bookingId, isAuthenticated, currentUser, getBookingById, fetchBookings, history]);

  // Load messages for this booking + poll
  useEffect(() => {
    const loadMessages = async () => {
      if (!isAuthenticated || !currentUser || !bookingId) return;
      setLoadingMessages(true);
      await fetchMessagesForBooking(bookingId);
      setLoadingMessages(false);
      markMessagesRead(bookingId);
    };

    loadMessages();

    const intervalId = setInterval(() => {
      if (isAuthenticated && currentUser && bookingId) {
        fetchMessagesForBooking(bookingId);
        markMessagesRead(bookingId);
      }
    }, 5000);

    return () => clearInterval(intervalId);
  }, [
    bookingId,
    isAuthenticated,
    currentUser,
    fetchMessagesForBooking,
    markMessagesRead,
  ]);

  if (!isAuthenticated) {
    return null;
  }

  if (loadingBooking) {
    return (
      <div className="chatPage chatPage--center">
        <CircularProgress />
      </div>
    );
  }

  if (!localBooking) {
    return (
      <div className="chatPage chatPage--center">
        <p>Booking not found.</p>
      </div>
    );
  }

  const otherParticipantName =
    currentUser.id === localBooking.student_id
      ? localBooking.landlord_name
      : localBooking.student_name;

  const listingTitle = localBooking.listing_title;
  const moveInDate = localBooking.move_in_date;
  const visitDate = localBooking.visitation_date;

  const handleSend = async () => {
    if (!input.trim() || sending) return;
    try {
      setSending(true);
      await sendMessage(bookingId, input);
      setInput('');
      markMessagesRead(bookingId);
    } catch (error) {
      console.error('sendMessage error:', error);
      alert('Failed to send message. Please try again.');
    } finally {
      setSending(false);
    }
  };

  const handleKeyDown = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSend();
    }
  };

  return (
    <div className="chatPage">
      {/* Top header */}
      <div className="chatPage__header">
        <div className="chatPage__headerLeft">
          <IconButton onClick={() => history.goBack()}>
            <ArrowBackIosNewIcon />
          </IconButton>
          <div>
            <h2>{otherParticipantName}</h2>
            <p className="chatPage__subtitle">
              {listingTitle ? listingTitle : 'Booking conversation'}
              {moveInDate && `  Move-in: ${moveInDate}`}
              {visitDate && `  Visit: ${visitDate}`}
            </p>
          </div>
        </div>
      </div>

      {/*  Payment / transaction warning */}
      <div className="chatPage__warning">
        <p>
           For your safety, <strong>do not send any payments, bank details, or sensitive
          personal information</strong> in this chat. Arrange payments only through
          safe, agreed-upon methods outside the platform.
        </p>
      </div>

      {/* Messages area */}
      <div className="chatPage__body">
        {loadingMessages ? (
          <div className="chatPage__loading">
            <CircularProgress size={24} />
          </div>
        ) : messages.length === 0 ? (
          <div className="chatPage__empty">
            <p>
              No messages yet. Use this chat to confirm visitation date, move-in date, and
              house rules  but not to send payments or financial details.
            </p>
          </div>
        ) : (
          <div className="chatPage__messages">
            {messages.map((m) => {
              const isMine = m.sender_id === currentUser.id;
              return (
                <div
                  key={m.id}
                  className={`chatPage__messageRow ${
                    isMine ? 'chatPage__messageRow--mine' : 'chatPage__messageRow--theirs'
                  }`}
                >
                  <div
                    className={`chatPage__messageBubble ${
                      isMine ? 'chatPage__messageBubble--mine' : 'chatPage__messageBubble--theirs'
                    }`}
                  >
                    <p className="chatPage__messageText">{m.message}</p>
                    <span className="chatPage__messageMeta">
                      {new Date(m.created_at).toLocaleTimeString([], {
                        hour: '2-digit',
                        minute: '2-digit',
                      })}
                      {isMine && m.read_at && <span>  Seen</span>}
                    </span>
                  </div>
                </div>
              );
            })}
            <div ref={messagesEndRef} />
          </div>
        )}
      </div>

      {/* Input bar */}
      <div className="chatPage__inputBar">
        <TextField
          placeholder="Type a message to confirm visitation or move-in details (no payments here)..."
          multiline
          maxRows={3}
          fullWidth
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={handleKeyDown}
          variant="outlined"
          size="small"
        />
        <IconButton
          onClick={handleSend}
          disabled={!input.trim() || sending}
          className="chatPage__sendButton"
        >
          {sending ? <CircularProgress size={20} /> : <SendIcon />}
        </IconButton>
      </div>
    </div>
  );
}

export default ChatPage;
