import React, {
  createContext,
  useContext,
  useEffect,
  useCallback,
  useMemo,
  useState,
} from 'react';
import { supabase } from './supabaseClient';

const AppContext = createContext(null);

export const useApp = () => {
  const context = useContext(AppContext);
  if (!context) throw new Error('useApp must be used within AppProvider');
  return context;
};

export const AppProvider = ({ children }) => {
  const [currentUser, setCurrentUser] = useState(null);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [loading, setLoading] = useState(true);

  const [listings, setListings] = useState([]);
  const [bookings, setBookings] = useState([]);
  const [favorites, setFavorites] = useState([]);

  // Chat: messages keyed by booking_id
  const [messagesByBooking, setMessagesByBooking] = useState({});

  const [filters, setFilters] = useState({
    roomType: 'all',
    priceRange: [0, 20000],
    amenities: [],
    gender: 'all',
    searchQuery: '',
  });

  // ========== PROFILE HELPERS ==========

  const ensureProfile = useCallback(async (user) => {
    if (!user) return null;

    const { data: existing, error: existingError } = await supabase
      .from('profiles')
      .select('*')
      .eq('id', user.id)
      .maybeSingle();

    if (existingError) throw existingError;
    if (existing) return existing;

    const metadata = user.user_metadata || {};
    const baseName = user.email ? user.email.split('@')[0] : 'User';

    const profileData = {
      id: user.id,
      email: user.email,
      name: metadata.name || baseName,
      // DB still uses 'student' for boarder
      user_type: metadata.user_type || 'student',
    };

    const { data: created, error: insertError } = await supabase
      .from('profiles')
      .insert([profileData])
      .select('*')
      .single();

    if (insertError) throw insertError;
    return created;
  }, []);

  const mapProfileToUserObj = useCallback((profile) => {
    if (!profile) return null;
    return {
      id: profile.id,
      email: profile.email,
      name: profile.name,
      user_type: profile.user_type,
      phone: profile.phone || null,
      avatar_url: profile.avatar_url || null,
      bio: profile.bio || null,
    };
  }, []);

  // ========== LISTINGS ==========

  const fetchListings = useCallback(async () => {
    try {
      const { data, error } = await supabase
        .from('listings')
        .select('*')
        .order('created_at', { ascending: false });

      if (error) throw error;
      setListings(data || []);
      return data || [];
    } catch (error) {
      console.error('Fetch listings error:', error);
      return [];
    }
  }, []);

  const addListing = useCallback(
    async (listing) => {
      console.log('AppContext: addListing called with:', listing);

      if (!currentUser) {
        throw new Error('You must be logged in to add a listing.');
      }

      if (currentUser.user_type !== 'landlord') {
        throw new Error('Only landlords can add listings.');
      }

      const imageArray = Array.isArray(listing.images) ? listing.images : [];
      const mainImage =
        listing.img ||
        imageArray[0] ||
        'https://images.unsplash.com/photo-1560185127-6ed189bf02f4?w=600';

      const listingData = {
        title: listing.title,
        description: listing.description,
        location: listing.location,
        distance: listing.distance || '',
        price: parseFloat(listing.price),
        room_type: listing.roomType,
        gender: listing.gender,
        amenities: listing.amenities || [],
        img: mainImage,
        images: imageArray,
        latitude: listing.latitude ? parseFloat(listing.latitude) : null,
        longitude: listing.longitude ? parseFloat(listing.longitude) : null,
        landlord_id: currentUser.id,
        landlord_name: currentUser.name,
        landlord_contact: listing.landlordContact,
        star: 0,
        available: true,
      };

      const { data, error } = await supabase
        .from('listings')
        .insert([listingData])
        .select()
        .single();

      if (error) {
        console.error('addListing DB error:', error);
        throw error;
      }

      setListings((prev) => [data, ...prev]);
      return data;
    },
    [currentUser]
  );

  const updateListing = useCallback(
    async (id, updates) => {
      if (!currentUser) throw new Error('Must be logged in');

      const listing = listings.find((l) => l.id === id);
      if (listing && listing.landlord_id !== currentUser.id) {
        throw new Error('You can only edit your own listings');
      }

      const { data, error } = await supabase
        .from('listings')
        .update(updates)
        .eq('id', id)
        .select()
        .single();

      if (error) throw error;
      setListings((prev) => prev.map((l) => (l.id === id ? data : l)));
      return data;
    },
    [currentUser, listings]
  );

  const deleteListing = useCallback(
    async (id) => {
      if (!currentUser) throw new Error('Must be logged in');

      const listing = listings.find((l) => l.id === id);
      if (listing && listing.landlord_id !== currentUser.id) {
        throw new Error('You can only delete your own listings');
      }

      const { error } = await supabase.from('listings').delete().eq('id', id);
      if (error) throw error;

      setListings((prev) => prev.filter((l) => l.id !== id));
    },
    [currentUser, listings]
  );

  const getUserListings = useCallback(() => {
    if (!currentUser || currentUser.user_type !== 'landlord') return [];
    return listings.filter((l) => l.landlord_id === currentUser.id);
  }, [currentUser, listings]);

  const getFilteredListings = useCallback(() => {
    return listings.filter((listing) => {
      if (filters.roomType !== 'all' && listing.room_type !== filters.roomType)
        return false;

      if (
        listing.price < filters.priceRange[0] ||
        listing.price > filters.priceRange[1]
      )
        return false;

      if (
        filters.gender !== 'all' &&
        listing.gender !== 'any' &&
        listing.gender !== filters.gender
      )
        return false;

      if (
        filters.amenities?.length > 0 &&
        !filters.amenities.every((a) => listing.amenities?.includes(a))
      )
        return false;

      if (filters.searchQuery) {
        const q = filters.searchQuery.toLowerCase();
        const text = `${listing.title || ''} ${listing.location || ''} ${
          listing.description || ''
        }`.toLowerCase();
        if (!text.includes(q)) return false;
      }

      return true;
    });
  }, [listings, filters]);

  const canBookListing = useCallback(
    (listingId) => {
      if (!currentUser) return false;
      if (currentUser.user_type !== 'student') return false; // boarder only
      const listing = listings.find((l) => l.id === listingId);
      if (listing && listing.landlord_id === currentUser.id) return false;
      return true;
    },
    [currentUser, listings]
  );

  const canEditListing = useCallback(
    (listingId) => {
      if (!currentUser) return false;
      if (currentUser.user_type !== 'landlord') return false;
      const listing = listings.find((l) => l.id === listingId);
      return !!listing && listing.landlord_id === currentUser.id;
    },
    [currentUser, listings]
  );

  // ========== BOOKINGS ==========

  const fetchBookingsForUser = useCallback(async (userObj) => {
    if (!userObj) return [];

    try {
      let query = supabase.from('bookings').select('*');

      if (userObj.user_type === 'student') {
        query = query.eq('student_id', userObj.id);
      } else {
        query = query.eq('landlord_id', userObj.id);
      }

      const { data, error } = await query.order('created_at', {
        ascending: false,
      });

      if (error) throw error;

      setBookings(data || []);
      return data || [];
    } catch (error) {
      console.error('Fetch bookings error:', error);
      return [];
    }
  }, []);

  const fetchBookings = useCallback(async () => {
    if (!currentUser) return [];
    return fetchBookingsForUser(currentUser);
  }, [currentUser, fetchBookingsForUser]);

  const createBooking = useCallback(
    async (listingId, moveInDate, visitationDate, notes) => {
      if (!currentUser) throw new Error('Must be logged in to book');
      if (currentUser.user_type !== 'student')
        throw new Error('Only boarders can make bookings');

      const listing = listings.find((l) => l.id === listingId);
      if (!listing) throw new Error('Listing not found');

      if (listing.landlord_id === currentUser.id) {
        throw new Error('You cannot book your own listing');
      }

      const { data, error } = await supabase
        .from('bookings')
        .insert([
          {
            listing_id: listingId,
            student_id: currentUser.id,
            student_name: currentUser.name,
            student_email: currentUser.email,
            landlord_id: listing.landlord_id,
            landlord_name: listing.landlord_name,
            listing_title: listing.title,
            price: listing.price,
            move_in_date: moveInDate,
            visitation_date: visitationDate || null,
            notes,
            status: 'pending',
          },
        ])
        .select()
        .single();

      if (error) throw error;

      setBookings((prev) => [data, ...prev]);
      return data;
    },
    [currentUser, listings]
  );

  const updateBookingStatus = useCallback(
    async (bookingId, status) => {
      if (!currentUser) throw new Error('Must be logged in');
      if (currentUser.user_type !== 'landlord')
        throw new Error('Only landlords can update booking status');

      const { data, error } = await supabase
        .from('bookings')
        .update({ status })
        .eq('id', bookingId)
        .select()
        .single();

      if (error) throw error;

      setBookings((prev) => prev.map((b) => (b.id === bookingId ? data : b)));
      return data;
    },
    [currentUser]
  );

  const getUserBookings = useCallback(() => {
    if (!currentUser) return [];
    return currentUser.user_type === 'student'
      ? bookings.filter((b) => b.student_id === currentUser.id)
      : bookings.filter((b) => b.landlord_id === currentUser.id);
  }, [currentUser, bookings]);

  const getBookingById = useCallback(
    (bookingId) => bookings.find((b) => b.id === bookingId) || null,
    [bookings]
  );

  // ========== FAVORITES ==========

  const fetchFavorites = useCallback(async (userId) => {
    if (!userId) return [];

    try {
      const { data, error } = await supabase
        .from('favorites')
        .select('listing_id')
        .eq('user_id', userId);

      if (error) throw error;

      const ids = data?.map((f) => f.listing_id) || [];
      setFavorites(ids);
      return ids;
    } catch (error) {
      console.error('Fetch favorites error:', error);
      return [];
    }
  }, []);

  const toggleFavorite = useCallback(
    async (listingId) => {
      if (!currentUser) throw new Error('Must be logged in to save favorites');
      if (currentUser.user_type !== 'student')
        throw new Error('Only boarders can save favorites');

      const isFav = favorites.includes(listingId);

      if (isFav) {
        const { error } = await supabase
          .from('favorites')
          .delete()
          .eq('user_id', currentUser.id)
          .eq('listing_id', listingId);

        if (error) throw error;

        setFavorites((prev) => prev.filter((id) => id !== listingId));
      } else {
        const { error } = await supabase
          .from('favorites')
          .insert([{ user_id: currentUser.id, listing_id: listingId }]);

        if (error) throw error;

        setFavorites((prev) => [...prev, listingId]);
      }
    },
    [currentUser, favorites]
  );

  const isFavorite = useCallback(
    (listingId) => favorites.includes(listingId),
    [favorites]
  );

  const getFavoriteListings = useCallback(
    () => listings.filter((l) => favorites.includes(l.id)),
    [listings, favorites]
  );

  // ========== CHAT / MESSAGES ==========

  const fetchMessagesForBooking = useCallback(
    async (bookingId) => {
      if (!currentUser) throw new Error('Must be logged in to view messages');
      if (!bookingId) return [];

      try {
        const { data, error } = await supabase
          .from('messages')
          .select('*')
          .eq('booking_id', bookingId)
          .order('created_at', { ascending: true });

        if (error) throw error;

        setMessagesByBooking((prev) => ({
          ...prev,
          [bookingId]: data || [],
        }));

        return data || [];
      } catch (error) {
        console.error('Fetch messages error:', error);
        return [];
      }
    },
    [currentUser]
  );

  const getMessagesForBooking = useCallback(
    (bookingId) => messagesByBooking[bookingId] || [],
    [messagesByBooking]
  );

  const sendMessage = useCallback(
    async (bookingId, text) => {
      if (!currentUser) throw new Error('Must be logged in to send messages');
      if (!bookingId) throw new Error('Missing booking id');
      if (!text || !text.trim()) return null;

      const booking = bookings.find((b) => b.id === bookingId);
      if (!booking) throw new Error('Booking not found');

      let recipientId = null;
      if (currentUser.id === booking.student_id) {
        recipientId = booking.landlord_id;
      } else if (currentUser.id === booking.landlord_id) {
        recipientId = booking.student_id;
      } else {
        throw new Error('You are not part of this booking');
      }

      const payload = {
        booking_id: bookingId,
        sender_id: currentUser.id,
        recipient_id: recipientId,
        message: text.trim(),
      };

      const { data, error } = await supabase
        .from('messages')
        .insert([payload])
        .select()
        .single();

      if (error) throw error;

      setMessagesByBooking((prev) => ({
        ...prev,
        [bookingId]: [...(prev[bookingId] || []), data],
      }));

      return data;
    },
    [currentUser, bookings]
  );

  const markMessagesRead = useCallback(
    async (bookingId) => {
      if (!currentUser) return;
      if (!bookingId) return;

      try {
        const { error } = await supabase
          .from('messages')
          .update({ read_at: new Date().toISOString() })
          .eq('booking_id', bookingId)
          .eq('recipient_id', currentUser.id)
          .is('read_at', null);

        if (error) throw error;

        setMessagesByBooking((prev) => {
          const msgs = prev[bookingId] || [];
          const now = new Date().toISOString();
          return {
            ...prev,
            [bookingId]: msgs.map((m) =>
              m.recipient_id === currentUser.id && !m.read_at
                ? { ...m, read_at: now }
                : m
            ),
          };
        });
      } catch (error) {
        console.error('markMessagesRead error:', error);
      }
    },
    [currentUser]
  );

  // ========== AUTH ==========

  const signup = useCallback(
    async (email, password, name, userType) => {
      setLoading(true);
      try {
        const cleanEmail = (email || '').trim();
        const cleanName = (name || '').trim();

        const { data, error } = await supabase.auth.signUp({
          email: cleanEmail,
          password,
          options: {
            data: {
              name: cleanName,
              user_type: userType,
            },
          },
        });

        if (error) throw error;
        if (!data.user) return null;

        if (!data.session) {
          return {
            id: data.user.id,
            email: data.user.email,
            pendingConfirmation: true,
          };
        }

        const profile = await ensureProfile(data.user);
        const userObj = mapProfileToUserObj(profile);

        setCurrentUser(userObj);
        setIsAuthenticated(true);

        fetchListings();
        if (userObj.user_type === 'student') {
          fetchFavorites(userObj.id);
        } else {
          setFavorites([]);
        }
        fetchBookingsForUser(userObj);

        return userObj;
      } catch (error) {
        console.error('Signup error:', error);
        throw error;
      } finally {
        setLoading(false);
      }
    },
    [ensureProfile, mapProfileToUserObj, fetchListings, fetchFavorites, fetchBookingsForUser]
  );

  const login = useCallback(
    async (email, password) => {
      setLoading(true);
      try {
        const cleanEmail = (email || '').trim();

        const { data, error } = await supabase.auth.signInWithPassword({
          email: cleanEmail,
          password,
        });
        if (error) throw error;

        if (data.user) {
          const profile = await ensureProfile(data.user);
          const userObj = mapProfileToUserObj(profile);

          setCurrentUser(userObj);
          setIsAuthenticated(true);

          fetchListings();
          if (userObj.user_type === 'student') {
            fetchFavorites(userObj.id);
          } else {
            setFavorites([]);
          }
          fetchBookingsForUser(userObj);

          return userObj;
        }

        return null;
      } catch (error) {
        console.error('Login error:', error);
        throw error;
      } finally {
        setLoading(false);
      }
    },
    [ensureProfile, mapProfileToUserObj, fetchListings, fetchFavorites, fetchBookingsForUser]
  );

  const logout = useCallback(async () => {
    console.log('AppContext: logout called');
    try {
      await supabase.auth.signOut();
    } catch (error) {
      console.error('Logout error (Supabase):', error);
    } finally {
      setCurrentUser(null);
      setIsAuthenticated(false);
      setBookings([]);
      setFavorites([]);
      setMessagesByBooking({});
    }
  }, []);

  // ========== INITIALIZATION & AUTH LISTENER ==========

  useEffect(() => {
    let mounted = true;

    const hydrateUserFromSession = async (user) => {
      try {
        const profile = await ensureProfile(user);
        const userObj = mapProfileToUserObj(profile);

        if (!mounted) return;

        setCurrentUser(userObj);
        setIsAuthenticated(true);

        await fetchListings();

        if (userObj.user_type === 'student') {
          await fetchFavorites(userObj.id);
        } else {
          setFavorites([]);
        }

        await fetchBookingsForUser(userObj);
      } catch (err) {
        console.error('hydrateUserFromSession error:', err);
      }
    };

    const init = async () => {
      // Always fetch listings so guests/anon can browse
      await fetchListings();

      try {
        const {
          data: { session },
        } = await supabase.auth.getSession();

        if (session?.user && mounted) {
          await hydrateUserFromSession(session.user);
        }
      } catch (error) {
        console.error('Init error:', error);
      } finally {
        if (mounted) setLoading(false);
      }
    };

    init();

    const {
      data: { subscription },
    } = supabase.auth.onAuthStateChange(async (event, session) => {
      console.log('Auth state change:', event);

      if (!mounted) return;

      // avoid doing heavy work on INITIAL_SESSION;
      // we already handled it via getSession() in init()
      if (event === 'INITIAL_SESSION') {
        return;
      }

      if (event === 'SIGNED_IN' && session?.user) {
        await hydrateUserFromSession(session.user);
      } else if (event === 'SIGNED_OUT') {
        console.log('Auth SIGNED_OUT: clearing local user state');
        setCurrentUser(null);
        setIsAuthenticated(false);
        setBookings([]);
        setFavorites([]);
        setMessagesByBooking({});
      }
    });

    return () => {
      mounted = false;
      subscription?.unsubscribe();
    };
  }, [
    fetchListings,
    ensureProfile,
    mapProfileToUserObj,
    fetchFavorites,
    fetchBookingsForUser,
  ]);

  // ========== CONTEXT VALUE ==========

  const value = useMemo(
    () => ({
      currentUser,
      isAuthenticated,
      loading,

      // auth
      login,
      signup,
      logout,

      // listings
      listings,
      fetchListings,
      addListing,
      updateListing,
      deleteListing,
      getUserListings,
      getFilteredListings,
      canBookListing,
      canEditListing,

      // bookings
      bookings,
      fetchBookings,
      createBooking,
      updateBookingStatus,
      getUserBookings,
      getBookingById,

      // favorites
      favorites,
      fetchFavorites,
      toggleFavorite,
      isFavorite,
      getFavoriteListings,

      // chat
      messagesByBooking,
      fetchMessagesForBooking,
      getMessagesForBooking,
      sendMessage,
      markMessagesRead,

      // filters
      filters,
      setFilters,
    }),
    [
      currentUser,
      isAuthenticated,
      loading,
      login,
      signup,
      logout,
      listings,
      fetchListings,
      addListing,
      updateListing,
      deleteListing,
      getUserListings,
      getFilteredListings,
      canBookListing,
      canEditListing,
      bookings,
      fetchBookings,
      createBooking,
      updateBookingStatus,
      getUserBookings,
      getBookingById,
      favorites,
      fetchFavorites,
      toggleFavorite,
      isFavorite,
      getFavoriteListings,
      messagesByBooking,
      fetchMessagesForBooking,
      getMessagesForBooking,
      sendMessage,
      markMessagesRead,
      filters,
      setFilters,
    ]
  );

  return <AppContext.Provider value={value}>{children}</AppContext.Provider>;
};
