import React, { useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { Button } from '@mui/material';
import FavoriteIcon from '@mui/icons-material/Favorite';
import './Favorites.css';
import { useApp } from './AppContext';

function Favorites() {
  const history = useHistory();
  const { loading, isAuthenticated, currentUser, getFavoriteListings, toggleFavorite } = useApp();

  const favoriteListings = getFavoriteListings();

  useEffect(() => {
    if (loading) return;

    if (!isAuthenticated || !currentUser) {
      history.push('/login');
    }
  }, [loading, isAuthenticated, currentUser, history]);

  if (loading) {
    return (
      <div className="favorites">
        <div className="favorites__container">
          <p>Loading</p>
        </div>
      </div>
    );
  }

  if (!currentUser) {
    return null;
  }

  if (currentUser.user_type !== 'student') {
    return (
      <div className="favorites">
        <div className="favorites__container">
          <h2>Saved Listings</h2>
          <p className="favorites__note">Saved listings are available for student accounts.</p>
          <Button variant="contained" onClick={() => history.push('/search')} style={{ marginTop: 12 }}>
            Browse Listings
          </Button>
        </div>
      </div>
    );
  }

  return (
    <div className="favorites">
      <div className="favorites__container">
        <div className="favorites__header">
          <h2>
            <FavoriteIcon style={{ marginRight: 8 }} />
            Saved Listings
          </h2>
          <Button variant="outlined" onClick={() => history.push('/search')}>
            Browse More
          </Button>
        </div>

        {favoriteListings.length === 0 ? (
          <div className="favorites__empty">
            <p>You have no saved listings yet.</p>
            <Button variant="contained" onClick={() => history.push('/search')} style={{ marginTop: 12 }}>
              Find a Dorm
            </Button>
          </div>
        ) : (
          <div className="favorites__list">
            {favoriteListings.map((listing) => (
              <div
                key={listing.id}
                className="favorites__item"
                onClick={() => history.push(`/listing/${listing.id}`)}
                role="button"
                tabIndex={0}
              >
                <img
                  className="favorites__image"
                  src={listing.img || (listing.images && listing.images[0]) || 'https://images.unsplash.com/photo-1560185127-6ed189bf02f4?w=600'}
                  alt={listing.title || 'Listing'}
                />

                <div className="favorites__info">
                  <h3>{listing.title}</h3>
                  <p className="favorites__location">{listing.location}</p>
                  <p className="favorites__price">{Number(listing.price || 0).toLocaleString()}/month</p>
                </div>

                <div className="favorites__actions" onClick={(e) => e.stopPropagation()}>
                  <Button
                    variant="outlined"
                    color="error"
                    onClick={async () => {
                      try {
                        await toggleFavorite(listing.id);
                      } catch (err) {
                        alert(err?.message || 'Failed to remove favorite');
                      }
                    }}
                  >
                    Remove
                  </Button>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}

export default Favorites;
