import React, { useEffect, useRef } from 'react';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';

// Fix for default marker icons in Leaflet (CRA + Webpack)
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
});

/**
 * LocationPickerMap
 * - Interactive Leaflet map with a draggable marker
 * - Calls onChange({ latitude, longitude }) when the marker moves
 * - Updates the marker when latitude/longitude props change
 */
function LocationPickerMap({ latitude, longitude, onChange, height = 400, zoom = 14 }) {
  const mapRef = useRef(null);
  const mapInstanceRef = useRef(null);
  const markerRef = useRef(null);

  // Keep latest onChange without reinitializing the map
  const onChangeRef = useRef(onChange);
  useEffect(() => {
    onChangeRef.current = onChange;
  }, [onChange]);

  // Capture initial center/zoom ONCE (so init effect doesn't depend on props)
  const initialConfigRef = useRef({
    lat: Number.isFinite(Number(latitude)) ? Number(latitude) : 17.6180,
    lng: Number.isFinite(Number(longitude)) ? Number(longitude) : 121.7320,
    zoom: Number.isFinite(Number(zoom)) ? Number(zoom) : 14
  });

  // Initialize map once
  useEffect(() => {
    if (!mapRef.current || mapInstanceRef.current) return;

    const { lat, lng, zoom: initialZoom } = initialConfigRef.current;

    try {
      const map = L.map(mapRef.current).setView([lat, lng], initialZoom);

      L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
        attribution: ' OpenStreetMap contributors',
        maxZoom: 19
      }).addTo(map);

      const marker = L.marker([lat, lng], { draggable: true }).addTo(map);
      marker.bindPopup(' Drag me to set the exact location').openPopup();

      marker.on('dragend', () => {
        const pos = marker.getLatLng();
        onChangeRef.current({ latitude: pos.lat, longitude: pos.lng });
      });

      mapInstanceRef.current = map;
      markerRef.current = marker;
    } catch (error) {
      console.error('LocationPickerMap initialization error:', error);
    }

    return () => {
      try {
        if (mapInstanceRef.current) {
          mapInstanceRef.current.remove();
          mapInstanceRef.current = null;
          markerRef.current = null;
        }
      } catch (err) {
        console.error('LocationPickerMap cleanup error:', err);
      }
    };
  }, []);

  // Update marker + view when coordinates change
  useEffect(() => {
    const map = mapInstanceRef.current;
    const marker = markerRef.current;
    if (!map || !marker) return;

    const lat = Number(latitude);
    const lng = Number(longitude);
    if (!Number.isFinite(lat) || !Number.isFinite(lng)) return;

    try {
      marker.setLatLng([lat, lng]);
      map.setView([lat, lng], map.getZoom());
    } catch (error) {
      console.error('LocationPickerMap update error:', error);
    }
  }, [latitude, longitude]);

  return <div ref={mapRef} style={{ height, width: '100%', borderRadius: 8, overflow: 'hidden' }} />;
}

export default LocationPickerMap;
