import React, { useEffect, useState } from 'react';
import './ApplyLandlord.css';
import { useHistory } from 'react-router-dom';
import { useApp } from './AppContext';
import { supabase } from './supabaseClient';
import {
  Button,
  TextField,
  Typography,
  Card,
  CardContent,
  CircularProgress,
  Alert,
  Snackbar,
  FormControlLabel,
  Checkbox,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
} from '@mui/material';

function ApplyLandlord() {
  const history = useHistory();
  const { currentUser, isAuthenticated } = useApp();

  const [fullName, setFullName] = useState('');
  const [businessName, setBusinessName] = useState('');

  // Files
  const [permitFile, setPermitFile] = useState(null);
  const [idFile, setIdFile] = useState(null);

  const [submitting, setSubmitting] = useState(false);
  const [loadingApp, setLoadingApp] = useState(false);

  const [existingApp, setExistingApp] = useState(null);

  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  // Terms & Conditions popup
  const [acceptedTerms, setAcceptedTerms] = useState(false);
  const [termsOpen, setTermsOpen] = useState(false);

  // Load any existing landlord application for this user
  useEffect(() => {
    const load = async () => {
      if (!currentUser) return;
      setLoadingApp(true);
      try {
        const { data, error } = await supabase
          .from('landlord_applications')
          .select('*')
          .eq('user_id', currentUser.id)
          .order('created_at', { ascending: false })
          .limit(1)
          .maybeSingle();

        if (error) {
          console.error('ApplyLandlord: fetch existing application error:', error);
        } else {
          setExistingApp(data || null);
        }
      } catch (err) {
        console.error('ApplyLandlord: unexpected error fetching application:', err);
      } finally {
        setLoadingApp(false);
      }
    };

    if (isAuthenticated && currentUser) {
      setFullName(currentUser.name || '');
      load();
    }
  }, [isAuthenticated, currentUser]);

  const handlePermitChange = (e) => {
    const file = e.target.files?.[0] || null;
    setPermitFile(file);
  };

  const handleIdChange = (e) => {
    const file = e.target.files?.[0] || null;
    setIdFile(file);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');

    if (!currentUser) {
      setError('You must be logged in to apply as a landlord.');
      return;
    }

    if (currentUser.user_type === 'landlord') {
      setError('You are already registered as a landlord.');
      return;
    }

    if (!permitFile) {
      setError('Business permit image is required.');
      return;
    }

    if (!idFile) {
      setError('Valid ID image is required.');
      return;
    }

    if (!acceptedTerms) {
      setError('You must agree to the Terms & Conditions.');
      return;
    }

    setSubmitting(true);
    try {
      const appStatus = existingApp?.status || null;
      if (appStatus === 'PENDING') {
        throw new Error('You already have a pending application.');
      }

      const userId = currentUser.id;

      // Upload permit
      
      const permitFileName = `permit-${Date.now()}-${permitFile.name}`;
      const permitPath = `permits/${userId}/${permitFileName}`;

      const { error: permitUploadError } = await supabase.storage
        .from('permits-bucket')
        .upload(permitPath, permitFile, {
          cacheControl: '3600',
          upsert: true,
        });

      if (permitUploadError) {
        console.error('ApplyLandlord: permit upload error:', permitUploadError);
        throw new Error('Failed to upload business permit. Please try again.');
      }


      
      const idFileName = `valid-id-${Date.now()}-${idFile.name}`;
      const idPath = `permits/${userId}/${idFileName}`;

      const { error: idUploadError } = await supabase.storage
        .from('permits-bucket')
        .upload(idPath, idFile, {
          cacheControl: '3600',
          upsert: true,
        });

      if (idUploadError) {
        console.error('ApplyLandlord: id upload error:', idUploadError);
        throw new Error('Failed to upload valid ID. Please try again.');
      }

      // Insert application row
      const { data: inserted, error: insertError } = await supabase
        .from('landlord_applications')
        .insert([
          {
            user_id: userId,
            email: currentUser.email,
            full_name: fullName || currentUser.name || '',
            business_name: businessName || null,
            permit_path: permitPath,
            permit_mime_type: permitFile.type || 'application/octet-stream',
            valid_id_path: idPath,
            valid_id_mime_type: idFile.type || 'application/octet-stream',
          },
        ])
        .select()
        .single();

      if (insertError) {
        console.error('ApplyLandlord: insert application error:', insertError);
        throw insertError;
      }

      setExistingApp(inserted);
      setSuccess(
        'Application submitted! We will review your valid ID and business permit. You will be notified once you are approved as a landlord.'
      );
      setPermitFile(null);
      setIdFile(null);
      setAcceptedTerms(false);
    } catch (err) {
      console.error('ApplyLandlord: submit error:', err);
      setError(
        err?.message ||
          'Failed to submit landlord application. Please double-check your files and try again.'
      );
    } finally {
      setSubmitting(false);
    }
  };

  const notLoggedIn = !isAuthenticated || !currentUser;
  const isLandlord = currentUser?.user_type === 'landlord';
  const appStatus = existingApp?.status || null;
  const hasPendingApp = appStatus === 'PENDING';

  if (notLoggedIn) {
    return (
      <div className="applyLandlord applyLandlord--center">
        <Card className="applyLandlord__card">
          <CardContent>
            <Typography variant="h6" gutterBottom>
              Apply as a landlord
            </Typography>
            <Typography variant="body2" color="textSecondary" gutterBottom>
              You need to log in before you can submit a landlord application.
            </Typography>
            <Button
              variant="contained"
              color="primary"
              onClick={() => history.push('/login')}
            >
              Go to login
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }

  if (isLandlord) {
    return (
      <div className="applyLandlord applyLandlord--center">
        <Card className="applyLandlord__card">
          <CardContent>
            <Typography variant="h6" gutterBottom>
              You are already a landlord
            </Typography>
            <Typography variant="body2" color="textSecondary" gutterBottom>
              Your account is already marked as landlord. You can now add and manage your
              listings.
            </Typography>
            <Button
              variant="contained"
              color="primary"
              onClick={() => history.push('/add-listing')}
            >
              List a property
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="applyLandlord">
      <div className="applyLandlord__inner">
        <Card className="applyLandlord__card">
          <CardContent>
            <Typography variant="h5" gutterBottom>
              Apply as a landlord
            </Typography>
            <Typography variant="body2" color="textSecondary" paragraph>
              To protect boarders and comply with local regulations, we require both a
              valid government-issued ID and a Business Permit before we upgrade your
              account to landlord. Your application will be manually reviewed.
            </Typography>

            {loadingApp && (
              <div className="applyLandlord__loading">
                <CircularProgress size={20} />
              </div>
            )}

            {existingApp && !loadingApp && (
              <Alert
                severity={
                  appStatus === 'APPROVED'
                    ? 'success'
                    : appStatus === 'REJECTED'
                    ? 'error'
                    : 'info'
                }
                sx={{ marginBottom: 2 }}
              >
                {appStatus === 'PENDING' && (
                  <>Your previous application is still pending review.</>
                )}
                {appStatus === 'APPROVED' && (
                  <>
                    Your application has been approved. If your account did not update
                    automatically, please log out and log back in.
                  </>
                )}
                {appStatus === 'REJECTED' && (
                  <>
                    Your last application was rejected
                    {existingApp.rejection_reason
                      ? `: ${existingApp.rejection_reason}`
                      : '.'}{' '}
                    You may submit a new application with corrected documents.
                  </>
                )}
              </Alert>
            )}

            <Snackbar
              open={!!error}
              autoHideDuration={8000}
              onClose={() => setError('')}
              anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            >
              <Alert
                onClose={() => setError('')}
                severity="error"
                sx={{ width: '100%', whiteSpace: 'pre-line' }}
              >
                {error}
              </Alert>
            </Snackbar>

            <Snackbar
              open={!!success}
              autoHideDuration={8000}
              onClose={() => setSuccess('')}
              anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            >
              <Alert
                onClose={() => setSuccess('')}
                severity="success"
                sx={{ width: '100%' }}
              >
                {success}
              </Alert>
            </Snackbar>

            <form onSubmit={handleSubmit} className="applyLandlord__form">
              <TextField
                label="Full name (as shown on ID/permit)"
                value={fullName}
                onChange={(e) => setFullName(e.target.value)}
                fullWidth
                margin="normal"
                required
                disabled={submitting || hasPendingApp}
              />

              <TextField
                label="Business name (optional)"
                value={businessName}
                onChange={(e) => setBusinessName(e.target.value)}
                fullWidth
                margin="normal"
                disabled={submitting || hasPendingApp}
              />

              <div className="applyLandlord__section">
                <Typography variant="subtitle1" gutterBottom>
                  Valid ID (required)
                </Typography>
                <Typography variant="body2" color="textSecondary" paragraph>
                  Upload a clear photo or scan of your valid government-issued ID
                  (e.g. passport, national ID, driver&apos;s license, PRC ID, etc.).
                </Typography>

                <input
                  type="file"
                  accept="image/*,application/pdf"
                  onChange={handleIdChange}
                  disabled={submitting || hasPendingApp}
                />

                {idFile && (
                  <Typography
                    variant="body2"
                    color="textSecondary"
                    sx={{ marginTop: 1 }}
                  >
                    Selected file: {idFile.name}
                  </Typography>
                )}
              </div>

              <div className="applyLandlord__section">
                <Typography variant="subtitle1" gutterBottom>
                  Business Permit (required)
                </Typography>
                <Typography variant="body2" color="textSecondary" paragraph>
                  Upload a clear photo or scanned copy of your valid Business Permit.
                </Typography>

                <input
                  type="file"
                  accept="image/*,application/pdf"
                  onChange={handlePermitChange}
                  disabled={submitting || hasPendingApp}
                />

                {permitFile && (
                  <Typography
                    variant="body2"
                    color="textSecondary"
                    sx={{ marginTop: 1 }}
                  >
                    Selected file: {permitFile.name}
                  </Typography>
                )}
              </div>

              <div className="applyLandlord__terms">
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={acceptedTerms}
                      onChange={(e) => setAcceptedTerms(e.target.checked)}
                      color="primary"
                      disabled={submitting || hasPendingApp}
                    />
                  }
                  label={
                    <span>
                      I confirm that my documents are valid and I agree to the{' '}
                      <button
                        type="button"
                        onClick={() => setTermsOpen(true)}
                        style={{
                          background: 'none',
                          border: 'none',
                          padding: 0,
                          margin: 0,
                          color: '#3498db',
                          cursor: 'pointer',
                          textDecoration: 'underline',
                          font: 'inherit',
                        }}
                      >
                        Terms &amp; Conditions and Privacy Notice
                      </button>
                      .
                    </span>
                  }
                />
              </div>

              <div className="applyLandlord__actions">
                <Button
                  variant="outlined"
                  onClick={() => history.push('/dashboard')}
                  disabled={submitting}
                >
                  Cancel
                </Button>
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  disabled={submitting || hasPendingApp}
                  sx={{ marginLeft: 2 }}
                >
                  {submitting ? <CircularProgress size={20} /> : 'Submit application'}
                </Button>
              </div>
            </form>
          </CardContent>
        </Card>
      </div>

      {/* Terms & Conditions dialog for landlord application */}
      <Dialog
        open={termsOpen}
        onClose={() => setTermsOpen(false)}
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle>Landlord Terms &amp; Conditions &amp; Privacy Notice</DialogTitle>
        <DialogContent dividers>
          <Typography variant="body2" gutterBottom>
            By applying as a landlord on DormFinds, you confirm that:
          </Typography>
          <Typography variant="body2" component="div" gutterBottom>
             You are the lawful owner or authorized representative of the property you
              intend to list. <br />
             The valid ID, business permit, and any documents you submit are true,
              accurate, and valid. <br />
             You will keep your listings accurate and up to date, including pricing and
              availability.
          </Typography>
          <Typography variant="body2" gutterBottom>
            We collect your name, email address, valid ID, and business permit details to
            verify your identity as a landlord and to help prevent fraud. Your data is
            processed in accordance with the Data Privacy Act of 2012 (Republic Act No.
            10173) of the Philippines.
          </Typography>
          <Typography variant="body2" gutterBottom>
            DormFinds is a listing and messaging platform. It does not handle payments,
            deposits, or rent collections for you. You are responsible for creating your
            own contracts, receipts, and payment arrangements directly with boarders.
          </Typography>
          <Typography variant="body2" gutterBottom>
            DormFinds and its developer are not liable for any financial loss, scams,
            disputes, or damages arising from your dealings with boarders or any third
            parties, whether online or offline. You agree not to hold DormFinds or its
            developer responsible for actions of other users.
          </Typography>
          <Typography variant="body2" gutterBottom>
            By proceeding, you consent to the collection and processing of your personal
            and business information for verification and platform safety, and you agree to
            comply with applicable laws, regulations, and school policies related to
            boarding houses and rentals.
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setTermsOpen(false)}>Close</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default ApplyLandlord;
