import { createClient } from '@supabase/supabase-js';

// Fetch Supabase URL and anon key from environment variables
const supabaseUrl = process.env.REACT_APP_SUPABASE_URL || '';
const supabaseAnonKey = process.env.REACT_APP_SUPABASE_ANON_KEY || '';

// DEBUG LOGGING (only for development mode)
if (process.env.NODE_ENV === 'development') {
  console.log('[Supabase debug] URL:', supabaseUrl || '(EMPTY)');
  console.log(
    '[Supabase debug] Anon key length:',
    supabaseAnonKey ? supabaseAnonKey.length : 0
  );
  if (supabaseAnonKey) {
    console.log(
      '[Supabase debug] Anon key prefix:',
      supabaseAnonKey.slice(0, 8) + '...'
    );
  }
}

// Warning if Supabase URL or anon key is missing
if (!supabaseUrl || !supabaseAnonKey) {
  console.warn(
    '[Supabase debug] Missing Supabase URL or anon key. Make sure your .env file is configured correctly.'
  );
}

// Create Supabase client, using the environment variables
export const supabase = createClient(
  supabaseUrl,
  supabaseAnonKey
);
