// src/App.js
import React from 'react';
import './App.css';

import Home from './Home';
import Header from './Header';
import Footer from './Footer';
import SearchPage from './SearchPage';
import Auth from './Auth';
import Dashboard from './Dashboard';
import AddListing from './AddListing';
import ListingDetail from './ListingDetail';
import EditListing from './EditListing';
import Favorites from './Favorites';
import ChatPage from './ChatPage';
import ApplyLandlord from './ApplyLandlord';
import AdminLandlords from './AdminLandlords';
import SupportDeveloper from './SupportDeveloper';
import NotFound from './NotFound';

import { AppProvider } from './AppContext';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

function App() {
  return (
    <AppProvider>
      <div className="app">
        <Router>
          <Header />
          <Switch>
            <Route exact path="/login">
              <Auth />
            </Route>

            <Route exact path="/dashboard">
              <Dashboard />
            </Route>

            <Route exact path="/add-listing">
              <AddListing />
            </Route>

            <Route exact path="/edit-listing/:id">
              <EditListing />
            </Route>

            <Route exact path="/listing/:id">
              <ListingDetail />
            </Route>

            <Route exact path="/favorites">
              <Favorites />
            </Route>

            <Route path="/bookings/:bookingId/chat" component={ChatPage} />

            <Route exact path="/search">
              <SearchPage />
            </Route>

            <Route exact path="/apply-landlord">
              <ApplyLandlord />
            </Route>

            <Route exact path="/admin/landlords">
              <AdminLandlords />
            </Route>

            <Route exact path="/support-developer">
              <SupportDeveloper />
            </Route>

            <Route exact path="/">
              <Home />
            </Route>

            <Route path="*">
              <NotFound />
            </Route>
          </Switch>
          <Footer />
        </Router>
      </div>
    </AppProvider>
  );
}

export default App;
