import React, { useEffect, useState } from 'react';
import { useApp } from './AppContext';
import { Button, CircularProgress, Typography } from '@mui/material';

function AdminLandlords() {
  const { fetchLandlordApplications, approveLandlordApplication, rejectLandlordApplication } = useApp();
  const [applications, setApplications] = useState([]);
  const [loading, setLoading] = useState(true);

  // Fetch applications when the component is mounted
  useEffect(() => {
    const loadApplications = async () => {
      setLoading(true);
      const fetchedApplications = await fetchLandlordApplications();  // Ensure this function is in AppContext
      setApplications(fetchedApplications);
      setLoading(false);
    };
    loadApplications();
  }, [fetchLandlordApplications]);

  const handleApprove = async (id) => {
    await approveLandlordApplication(id);  // Implement this in AppContext
    setApplications((prev) => prev.filter((app) => app.id !== id)); // Remove approved application from the list
  };

  const handleReject = async (id) => {
    await rejectLandlordApplication(id);  // Implement this in AppContext
    setApplications((prev) => prev.filter((app) => app.id !== id)); // Remove rejected application from the list
  };

  if (loading) {
    return (
      <div className="loading">
        <CircularProgress />
      </div>
    );
  }

  return (
    <div>
      <Typography variant="h5">Landlord Applications</Typography>
      <div>
        {applications.length === 0 ? (
          <Typography>No applications pending approval.</Typography>
        ) : (
          applications.map((application) => (
            <div key={application.id} style={{ marginBottom: '16px', padding: '10px', border: '1px solid #ccc' }}>
              <Typography variant="h6">{application.full_name}</Typography>
              <Typography variant="body2">{application.email}</Typography>
              <div>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => handleApprove(application.id)}
                >
                  Approve
                </Button>
                <Button
                  variant="outlined"
                  color="secondary"
                  onClick={() => handleReject(application.id)}
                  style={{ marginLeft: '8px' }}
                >
                  Reject
                </Button>
              </div>
            </div>
          ))
        )}
      </div>
    </div>
  );
}

export default AdminLandlords;
